import { Container } from "./styles";
import { Video } from "../videos";

const FuncaoParametro = () => {
    const texto1 = "const somar = (parametro1, parametro2) => {";
    const texto2 = "var resultado = parametro1 + parametro2;";
    const texto3 = "console.log(`O resultado da soma é de: ` + resultado);";
    const texto4 = "}";
    const texto5 = "somar(10, 55);";
    const texto6 = "somar(10);";

    return(
        <Container>
            <h2>Parâmetros de uma função</h2>

            <p>Agora vamos aprender a enviar parâmetros (dados externos) para serem utilizados em uma função.</p>

            <p>Mas antes vamos entender o que é um parâmetro e para isso vamos criar uma função chamada somar que recebe dois parametros para podermos fazer uma ação de soma, esses valores são recebidos dentro dos parênteses da função.</p>

            <p>Aí fica a dúvida, de onde vêm esse valores? Fique tranquilo, pois eles vêm de onde estamos chamando (dando a ordem de agir) a função, vamos deixar exemplos aqui abaixo para você entender melhor.</p>

            <p>Nesse primeiro exemplo temos uma função chamada somar que precisa de dois parâmetros para poder agir, parametro1 e parametro2, e os dados que eles irão receber estão sendo enviados logo depois com "somar(10, 55)". Após isso, a variável resultado está recebendo a soma dos dois parâmetros e o resultado é impresso no navegador com o console.log.</p>

            <div className="function">
                <p>
                    <span>{texto1}</span> <br />
                        <span className="spanSpace">{texto2}</span> <br />
                        <span className="spanSpace">{texto3}</span> <br />
                    <span>{texto4}</span><br /><br />
                    <span>{texto5}</span>
                </p>
                <p>Impressão no console: O resultado da soma é de: 65</p>
            </div>

            <p>Caso a função esteja esperando dois parâmetros e só receber um o resultado será um erro, nesse caso se você retirar um dos dois acima a impressão será "NaN".</p>

            <div className="function">
                <p>
                    <span>{texto1}</span> <br />
                        <span className="spanSpace">{texto2}</span> <br />
                        <span className="spanSpace">{texto3}</span> <br />
                    <span>{texto4}</span><br /><br />
                    <span>{texto6}</span>
                </p>
                <p>Impressão no console: O resultado da soma é de: NaN</p>
            </div>

            <p>Para melhor aprendizado, recomendo que você faça algumas funções e tente ir modificando os dados, afinal quanto mais treinamos mais aprendemos. Bons estudos e te vejo na próxima.</p>

            {/* <h3>Assista essa aula para entender melhor</h3>

            <Video link='https://www.youtube.com/embed/E-p5NoFjFOo' /> */}

        </Container>
    );
}

export default FuncaoParametro;