import { Container } from "./styles";
import IMG1 from '../../assets/projetojs1img1.jpg';
import IMG2 from '../../assets/projetojs1img2.jpg';

const Projeto1 = () => {
    return(
        <Container>
            <h2>Juntando nomes com Javascript</h2>

            <h3>Pegando dados e inserindo no DOM</h3>
            <p>Antes de iniciar esse projeto espero que você ja tenha visto nossa introdução sobre Javascript, pois cada detalhe que vamos aprendendo pode fazer a diferença em um projeto maior.</p>

            <h3>Atenção!</h3>

            <p>Para melhor visualização, esse projeto estará com o HTML, CSS e Javascript no mesmo documento.</p>

            <p>No projeto fazemos o uso de três inputs, sendo 2 para digitar os nomes e o terceiro para exibir a informação. Existem vários tipos de inputs para os mais diversificados fins. Por exemplo temos os types: button, checkbox, color, date, datetime, email, entre outros. Caso você não conheça os inputs recomendo que dê uma pesquisada, mesmo não conseguindo absorver todo o conteúdo de uma vez, você já saberá as formas de input e o que eles são capazes de fazer por você.</p>

            <p>Já na parte do Javascript temos a function somar que está recebendo os valores, colocando-os em variáveis e mandando-os para a tela com o res.innerHTML.</p>

            <p>Existem algumas formas de acessar o HTML pelo Javascript, uma delas é o getElementById, com ele conseguimos pegar um elemento pelo "id", temos o id="txtn1" como exemplo que está recebendo o valor digitado e a variável tn1 o recebe através do window.document. getElementById('txtn1').</p>

            <p>Outra forma de acessar o HTML é com o querySelector, com ele podemos pegar o conteúdo pelo id e pela class, diferente do getElementById, ele pode selecionar mais de uma class com querySelectorAll, no qual irá pegar todos os elementos com a class selecionada.</p>

            <p>Por último temos o comando res.innerHTML que está pegando os valores recolhidos nas variáveis e enviando para o DOM. Note que além de enviar o conteúdo das variáveis também conseguimos enviar textos, desde que esteja usando as aspas invertidas, dessa forma temos uma template string, que nos permite juntar texto e código.</p>

            <div className="images">
                <img src={IMG1} alt="imagem com exemplo de código CSS" />
            </div>
            <div className="images">
                <img src={IMG2} alt="imagem do resultado no navegador" />
            </div>

        </Container>
    );
}

export default Projeto1;