import { Container } from "./styles";
import IMG1 from '../../assets/js/funcao/image1.jpg';
import IMG2 from '../../assets/js/funcao/image2.jpg';
import { Video } from "../videos";

const Funcao = () => {
    const texto1 = "( ) => { }"

    return(
        <Container>
            <h2>Funcões</h2>

            <h3>O que é uma função?</h3>
            <p>Uma função é um procedimento de JavaScript em que um conjunto de instruções que executa uma tarefa/ação ou calcula um valor de qualquer tipo.</p>

            <p>Abaixo temos duas das formas mais utilizadas de criar funções.</p>

            <div className="images">
                <img src={IMG1} alt="Imagem com exemplo de código Javascript" />
            </div>

            <p>Na primeira criamos uma function e damos um nome a ela e damos a ordem que ela executará. Após ela estamos chamando ela com o exemplo1(), então assim que o documento for renderizado ela será ativa.</p>

            <p>Na segunda temos uma constante recebendo uma arrow function, que chamamos de função anônima, particularmente acho essa a forma mais utilizada.</p>

            <p>Deixando claro que se a const não receber uma função anônima ( {texto1} ) ela não funciona como função.</p>

            <h3>Função sendo ativada</h3>

            <p>Assim como já foi explicado, apenas criar uma função não é o suficiente para que ela funcione, temos que fazer o chamado por ela para que ela seja ativada e execute e ordem que está armazenada nela. Veja na imagem abaixo um exemplo para entender melhor.</p>

            <div className="images">
                <img src={IMG2} alt="Imagem com exemplo de código Javascript" />
            </div>

            <p>Nesse exemplo temos uma arrow function que tem armazenado um if que se for confirmado irá executar o console.log. Após isso temos a chamada com o exemplo3(), então assim que o documento for renderizado a função será ativa.</p>

            <p>Para absorver melhor o conteúdo tente criar suas funções e brincar misturando com if else e switch.</p>

            <h3>Assista essa aula para entender melhor</h3>

            <Video link='https://www.youtube.com/embed/E-p5NoFjFOo' />

        </Container>
    );
}

export default Funcao;