import { Container } from "./styles";

import IMG1 from '../../assets/react1.jpg';
import IMG2 from '../../assets/react2.jpg';
import IMG3 from '../../assets/react3.jpg';
import IMG4 from '../../assets/react4.jpg';
import IMG5 from '../../assets/react5.jpg';
import { Link } from "react-router-dom";

const ReactJS1 = () => {
    return(
        <Container>
            <h3>O que é ReactJS?</h3>

            <p>O React é uma biblioteca feita em linguagem Javascript, criada pela empresa do Facebook para ajudar a reduzir o processamento e melhorar o desenpenho, mesmo em máquinas de pouca memória de processamento.</p>

            <h3>Como o React funciona?</h3>

            <p>O React funciona com o Virtual DOM. Praticamente o React deixa o conteúdo pré-carregado no Virtual DOM, quando em algum momento fazemos uma mudança de conteúdo com click, rolagem ou alguma requisição, o conteúdo que será alterado é o do Virtual DOM, e após realizar essa alteração ele compara o Vitual DOM com o DOM de fato e executa a alteração apenas do conteúdo que está diferente entre eles.</p>

            <p>Então o que vemos carregado em nossa tela é o que chamamos de SPA (Single Page Application). Quando o documento é executado ele carrega o HTML com apenas uma div, e é o conteúdo dessa div que será manipulado pelo React com a troca pré-carregada entre o Vitual DOM e o DOM da nossa máquina.</p>

            <h3>O que são os componentes do React?</h3>

            <p>O React é baseado em uma estrutura de componentes, isso significa que o projeto final é resultado de vários componentes que contem uma parte de código criados de forma separada e interligados através de exportação e importação.</p>

            <h3>Qual a vantegem do uso de componentes no React?</h3>

            <p>Por cada parte do projeto ser criada em seu próprio componente a manutenção se torna mais fácil, já que basta identificar o componente e fazer sua alteração de forma separada sem alterar os dados dos outros componentes.</p>

            <p>Além dos componentes facilitarem na manuntenção do projeto, também é possivel fazer a reutilização dos componentes, isso significa que se houver algum trecho de código que será repetido em vários locais do projeto só será preciso criar um componente para ele e utilizá-lo quando for necessário.</p>

            <h3>Como podemos ter um exemplo da reutilização de componentes?</h3>

            <p>Podemos usar os botões como exemplo e criá-los em componentes separados, já que normalmente são trechos de código que se repetem em um projeto e utilizá-los quando for preciso, dessa forma iremos, além de manter um projeto mais organizado, deixar um modelo padronizado para todos os botões do projeto.</p>

            <h3>O que precisamos ter para utilizar o ReactJS?</h3>

            <p>Para trabalhar com o ReactJS precisamos ter o NodeJS instalado em nosso computador. Para saber se já temos ele instalado devemos abrir o nosso prompt de comando (caso esteja no windows, se estiver em um MacBook é terminal), e para isso iremos dar o comando "npm -v" no nosso prompt de comando e irá aparecer a versão que você tem instalada, e caso não tenha é só ir no site do NodeJS e fazer o download para instalação.</p>

            <p>Para fazer a instalação você deve ir em "<Link to='https://nodejs.org' target={"_blank"}>https://nodejs.org</Link>", depois ir em downloads. Se sua máquina for de 32-bit você escolhe ele, caso seja 64-bit você escolhe ele.</p>

            <div className="images">
                <img src={IMG1} alt="Imagem do site do NodeJS" />
            </div>

            <p>Depois de instalado você irá novamente no prompt de comando e digitar o comando "node -v" para confirmar se foi instalado e ver a versão.</p>

            <p>Quando instalamos o NodeJS ele vem também com o NPM (Node Package Manager) que é uma ferramenta que iremos utilizar muito, e para ver a versão do NPM é só dar o comando "npm --version".</p>

            <div className="images">
                <img src={IMG2} alt="Imagem do prompt de comando mostrando as versões do NodeJS e do NPM" />
            </div>

            <p>Após essas verificações nós já podemos começar a criar nosso projetos ReactJS.</p>

            <h3>Como criamos um projeto React?</h3>

            <p>Iremos escolher a pasta que iremos criar nosso projeto. Em nosso exemplo vamos criar da forma recomendada, ou seja, criar uma pasta (a nossa será projetos) nova no disco local para armazenar todos os seus projetos de forma organizada.</p>

            <p>Abrimos o nosso prompt de comando e ele mostrará que estamos em C:\Users\usuario, agora vamos voltar duas pastas com o comando "cd.." e depois "cd.." novamente e depois "cd projetos", agora estamos dentro da nossa pasta projetos e iremos iniciar a criação.</p>

            <p>Vamos criar utilizando o Create React App que é a técnica oficial de criação de projetos React. Vamos dar o comando "npx create-react-app react-primeiro --template typescript", isso significa que estamos criando nosso projeto com o método CRA, ele será chamado de react-primeiro e ele já será criado habilitado para o uso de Typescript. Esse processo de criação pode levar alguns minutos, principalmente se for a primeira vez que você estiver criando um projeto.</p>

            <p>Agora que temos nosso projeto criado, podemos fechar o prompt, ir até nossa pasta projetos, clicar com o botão direito na pasta do nosso projeto e abrir ele com o VSCode. Pronto, agora vamos entender a estrutura do nosso projeto, pois ele será criado com vários arquivos e temos que saber os que iremos utilizar de início.</p>

            <div className="images">
                <img src={IMG3} alt="Imagem do VSCode mostrando o projeto criado" />
            </div>

            <p>Veja que ao abrir o projeto no VSCode temos algumas pastas criadas, e é na pasta "src" que iremos focar. Nela temos o "app.tsx" que é o arquivo que será renderizado através do arquivo "index.tsx" e mostrado na tela.</p>

            <h3>O uso do JSX no React</h3>

            <p>JSX significa "JavaScript Xml", que é um código Javascript que utiliza sintaxe HTML para escrita de código. O JSX é uma forma de criar o código mais fácil, veja abaixo um exemplo feito utilizando o JSX e outro sem usar.</p>

            <p>No primeiro (sem o JSX) utilizamos os três parâmetros necessários para criação dentro de um React.createElement( 1°, 2°, 3°), onde o primeiro é a tag, o segundo a referência dela e o terceiro o conteúdo.</p>

            <div className="images">
                <img src={IMG4} />
            </div>

            <p>Já no segundo criamos como se fosse HTML dentro de código Javascript e o compilador Babel faz o trabalho de levar os dados da forma que ele necessita para a impressão na tela.</p>

            <div className="images">
                <img src={IMG5} />
            </div>

            <h3>Como o JSX é interpretado?</h3>

            <p>Para que o JSX seja interpretado pelos navegadores é necessário que seja transformado em código Javascript comum através de alguma ferramenta, como a Babel por exemplo, que é um compilador Javascript feito por um conjunto de ferramentas, e uma de suas funções é converter JSX para ser lido pelos navegadores como Javascript.</p>

            <h3>O uso de "ClasseName" para identificar objetos</h3>

            <p>O termo "class" já é reservado para o Javascript, então para não haver conflitos, o termo "ClassName" foi aderido como atributo nos objetos JSX.</p>

            <h3>Como rodar o projeto?</h3>

            <p>Ao abrir o terminal no editor de códigos iremos digitar o comando "npm run start" que é o comando que vem como padrão para inicializar o projeto em nossa máquina. Com isso será aberta uma aba no navegador com um indereço local da máquina "http://localhost:3000". Para confirmar essa informação você pode abrir o arquivo "package.json" e descer até onde ficam os "scripts" e ver quais os comandos podem ser utilizados.</p>

        </Container>
    );
}

export default ReactJS1;