import styled from "styled-components";

export const Container2 = styled.div`
    margin-top: var(--navHeight);
    padding: 5rem 2rem;
    font-size: 2rem;
    text-align: center;
    max-width: 110rem;
    margin-inline: auto;
    line-height: 2.2rem;
    letter-spacing: .2rem;  

    h2 {
        margin-bottom: 5rem;
        text-align: center;
    }

    ul {
        margin-inline: auto;
        /* width: 50vw; */
        padding: 2rem;
        border: 2px solid var(--bgColorHover);
        border-radius: 1rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        gap: .5rem;
    }
    
    li {
        width: fit-content;
        list-style: none;
        cursor: pointer;

        :hover {
            text-decoration: underline;
        }
    }
`;

export const Container = styled.div`
    margin-top: var(--navHeight);
    padding: 5rem 2rem;
    font-size: 1.7rem;
    text-align: justify;
    max-width: 110rem;
    margin-inline: auto;
    line-height: 2.2rem;
    letter-spacing: .2rem;

    li {
        padding-bottom: .5rem;
    }

    .images {
        margin-block: 5rem;
        margin-inline: auto;
        padding-block: 2rem;
        max-width: 80rem;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 2rem;

        img {
            width: 100%;
        }
    }

    a {
        text-decoration: none;
        color: var(--headerColor);

        :hover {
            text-decoration: underline;
        }
    }
`;