import styled from "styled-components";
import ME from "../../assets/me-about.png";

export const Container = styled.div`
    margin-top: var(--navHeight);
    background-color: var(--headerColor);
    color: var(--color1);
    overflow: hidden;
    height: 55rem;
    
    font-family: Roboto;
    
    .all {
        max-width: 110rem;
        margin-inline: auto;
        height: 100%;
        background-image: url(${ME});
        background-size: 38rem;
        background-repeat: no-repeat;
        background-position: right bottom;
        display: flex;
        flex-direction: column;
    }

    .text {
        width: 100%;
        display: flex;
        flex-direction: column;
        padding-inline: 2rem;
        padding-top: 6rem;
    }

    h1 {
        margin: 0;
        font-size: 4rem;
        font-family: Arial, Helvetica, sans-serif;
        text-align: start;
        max-width: 90vw; 
        color: var(--borderColor);
        margin-left: -.3rem;
    }

    h3 {
        max-width: 90vw; 
        margin: 0;
        font-size: 2rem;
        text-align: start;
        letter-spacing: .15rem;
    }

    .btn {
        width: 10rem;
        border: 1px solid var(--color1);
        color: var(--color1);
        border-radius: 5rem;
        text-decoration: none;
        font-size: 2rem;
        text-align: center;
        padding: 1rem;
        margin-top: 3rem;
        transition: ease-in-out .3s;
        display: none;

        :hover {
            background-color: var(--color1);
            color: var(--headerColor);
        }
    }

    .logos {
        /* padding-top: 6rem; */
        height: fit-content;
        overflow: hidden;
        display: flex;
        justify-content: end;
        padding: 4rem 5rem 4rem 0;

        img {
            width: 6rem;
            margin-left: 0;
        }

        .img1,
        .img4 {
            margin-top: 2rem;
        }

        .img2,
        .img3 {
            margin-bottom: 2rem;
        }
    }

    @media screen and (min-width: 375px) {
        height: 57rem;

        h1 {
            font-size: 4.7rem;
        }
    }

    @media screen and (min-width: 768px) {
        height: 45rem;
        
        .all {
            background-size: 43rem;
            display: grid;
            grid-template-columns: 1fr 1fr;
        }

        h1 {
            font-size: 6rem;
        }

        .btn {
            display: block;
        }

        .logos {
            padding: 5rem 7.5rem 4rem 0;

            img {
                width: 6rem;
            }
        }
    }

    @media screen and (min-width: 1024px) {
        height: 50rem;
        
        .all {
            background-size: 54rem;
        }

        .logos {
            padding: 2rem 10rem 4rem 0;

            img {
                width: 8rem;
            }
        }
    }
`;