import styled from "styled-components";

export const Container = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    nav {
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        background-color: rgb(57, 139, 255);
    }

    .close {
        font-size: 2rem;
    }

    h1 {
        font-size: 30px;
        color: aliceblue;
    }

    h2 {
        text-align: center;
        max-width: 400px;
        font-size: 25px;
    }

    h3 {
        font-size: 20px;
    }

    p {
        width: 100%;
        text-indent: 30px;
        text-align: justify;
        font-size: 18px;
        line-height: 22px;
        margin-bottom: 30px;
    }

    .menu {
        height: 100%;
        cursor: pointer;
    }

    .itens {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        transition: .4s;
    }

    .itens2 {
        display: none;
    }

    ul {
        gap: 5px;
    }

    li {
        list-style: none;
        font-size: 25px;
        cursor: pointer;
        padding: 5px;
        color: aliceblue;
    }

    img {
        height: 100%;
    }

    nav .topNav {
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 40px;
        padding-top: 20px;
        padding-inline: 20px;
    }

    main {
        width: 100%;
    }

    main .inicio {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    main .itensInicio {
        max-width: 300px;
        padding: 10px;
        display: flex;
        flex-direction: column;
        align-items: center;
        background-color: rgba(57, 140, 255, 0.152);
        border-radius: 30px;
        margin-block: 40px;
    }

    .itensInicio img {
        padding: 40px;
    }

    #button1 {
        padding: 10px;
        background-color: rgb(255, 0, 217);
        color: aliceblue;
        font-size: 16px;
        font-weight: bold;
        cursor: pointer;
        width: 150px;
        border-radius: 25px;
    }

    #button1:hover {
        text-decoration: underline;
    }

    #button2 {
        padding: 10px;
        background-color: rgb(255, 200, 0);
        color: aliceblue;
        font-size: 16px;
        font-weight: bold;
        cursor: pointer;
        width: 150px;
        border-radius: 25px;
    }

    #button2:hover {
        text-decoration: underline;
    }

    #button3 {
        padding: 10px;
        background-color: rgb(56, 122, 184);
        color: aliceblue;
        font-size: 16px;
        font-weight: bold;
        cursor: pointer;
        width: 150px;
        border-radius: 25px;
    }

    #button3:hover {
        text-decoration: underline;
    }

    a {
        text-decoration: none;
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 0 auto 50px auto;
        background-color: rgb(180, 180, 255);
        color: rgb(3, 3, 112);
        width: 5rem;
        height: 1.8rem;
        border-radius: 2.5rem;
        transition: .4s;
    }

    a:hover {
        text-decoration: underline;
        transform: scale(1.1);
    }

    @media screen and (min-width: 450px) {
        main .itensInicio {
            max-width: 350px;
            padding: 20px;
        }
    }

    @media screen and (min-width: 500px) {
        main .itensInicio {
            padding: 30px;
        }
    }

    @media screen and (min-width: 900px) {
        .grid {
            display: grid;
            grid-template-columns: 1fr 1fr;
            gap: 40px;
        }
    }

    @media screen and (min-width: 1330px) {
        .grid {
            display: grid;
            grid-template-columns: 1fr 1fr 1fr;
            gap: 40px;
        }
    }

    @media screen and (min-width: 1000px) {
        .menu {
            display: none;
        }

        nav {
            display: flex;
            flex-direction: row;
            height: 80px;
        }

        .itens {
            display: none;
        }

        .itens2 {
            height: 100%;
            display: flex;
            flex-direction: row;
            justify-content: center;
            align-items: center;
            gap: 20px;
            margin: 0;
            padding-inline: 20px;
        }

        li {
            display: flex;
            justify-self: center;
            align-items: center;
            height: 100%;
        }
    }
`;