import { Container } from "./styles";
import IMG1 from '../../assets/js/condicionalternario/image1.jpg';
import { Video } from "../videos";

const CondicionalTernario = () => {
    const texto1 = "{execute isso}";
    const texto2 = "{execute esse outro}"
    const texto3 = ">=";

    return(
        <Container>
            <h2>Condicional Ternário</h2>

            <h3>O que seria um condicional ternário?</h3>
            <p>Seria um if else feito com menos código, enquanto o if else precisa de algumas linhas, um condicional ternário é feito em apenas uma linha. Vamos ver uma condicional feita com if else e com condicional ternário para entender a diferença.</p>

            <p>If else: if(idade == true) {texto1} else {texto2}</p>

            <p>Ternário: let idade = correto ? "Questão correta" : "Questão errada"</p>

            <p>No ternário estamos dizendo o seguinte, o valor que a variável idade vai receber depende de correto, se correto existir e for verdadeiro a variável recebe o primeiro valor (Questão correta), caso contrário recebe o segundo valor(questão errada).</p>

            <p>Além de fazer com valores exatos podemos utilizar sinais condicionais como por exemplo: let nascimento = dataNascimento {texto3} 2010 ? "true" : "false".</p>

            <p>Veja na imagem abaixo alguns exemplos e tente recriá-los para ver o resultado. Aproveita e cria alguns exemplos como exercício de fixação.</p>

            <div className="images">
                <img src={IMG1} alt="Imagem com exemplo de código Javascript" />
            </div>

            <h3>Assista essa aula para entender melhor</h3>

            <Video link='https://www.youtube.com/embed/NPPl8ko0MGY' />

        </Container>
    );
}

export default CondicionalTernario;