import { Container } from "./styles";
import { Video } from "../videos";

const ManipulandoDom2 = () => {
    const texto1 = 'function clicar () {';
    const texto2 = "document.getElementById('titulo').append(' com texto adicional');";
    const texto3 = '}';
    const texto4 = "let ul = document.querySelector('ul');";
    const texto5 = 'let li = document.createElement("li");';
    const texto6 = 'li.innerText = "Texto 3 adicionado";';
    const texto7 = 'ul.appendChild(li);';
    const texto8 = 'ul.prepend(li);';

    return(
        <Container>
            <h2>Manipulando o DOM 2</h2>

            <p>Vamos agora usar o append() que é um comando que insere conteúdo extra no conteúdo que ja existe no elemento. Vamos fazer nosso exemplo no título, veja logo abaixo.</p>

            <div className="function">
                <p>
                    <span>{texto1}</span><br />
                    <span className="spanSpace">{texto2}</span><br />
                    <span>{texto3}</span>
                </p>
            </div>

            <p>O append funciona apenas com conteúdo de texto, e se você acionar ele mais de uma vez ele será executado mais de uma vez, por exemplo na função acima, se você clicar no botão duas vezes o conteúdo sera adicionado duas vezes, então é bom ter cuidado ao usaro append.</p>

            <p>Agora vamos conhecer o appendChild que adiciona o conteúdo que criamos no final do nosso elemento selecionado, veja abaixo o passo a passo de como usar.</p>

            <div className="function">
                <p>
                    <span>{texto1}</span><br />
                    <span className="spanSpace">{texto4}</span><br />
                    <span className="spanSpace">{texto5}</span><br />
                    <span className="spanSpace">{texto6}</span><br />
                    <span className="spanSpace">{texto7}</span><br />
                    <span>{texto3}</span>
                </p>
            </div>

            <p>Também temos o prepend que adiciona ao inicio do elemento selecionado. Vamos utilizar a mesma função e modificar apenas o appendChild para o prepend.</p>

            <div className="function">
                <p>
                    <span>{texto1}</span><br />
                    <span className="spanSpace">{texto4}</span><br />
                    <span className="spanSpace">{texto5}</span><br />
                    <span className="spanSpace">{texto6}</span><br />
                    <span className="spanSpace">{texto8}</span><br />
                    <span>{texto3}</span>
                </p>
            </div>

            <p>Caso você coloque nessa função o appendChild e o prepend, o que será executado é o último, no caso da função a seguir o que prevalecerá é o appendChild(li).</p>

            <div className="function">
                <p>
                    <span>{texto1}</span><br />
                    <span className="spanSpace">{texto4}</span><br />
                    <span className="spanSpace">{texto5}</span><br />
                    <span className="spanSpace">{texto6}</span><br />
                    <span className="spanSpace">{texto7}</span><br />
                    <span className="spanSpace">{texto8}</span><br />
                    <span>{texto3}</span>
                </p>
            </div>

            <p>Tenta fazer alguns exemplos, e nos vemos na próxima aula.</p>

            {/* <h3>Assista essa aula para entender melhor</h3>

            <Video link='https://www.youtube.com/embed/E-p5NoFjFOo' /> */}

        </Container>
    );
}

export default ManipulandoDom2;