import styled from "styled-components";

export const Container = styled.div`
    margin-top: var(--navHeight);
    padding: 5rem 2rem;
    font-size: 1.7rem;
    text-align: justify;
    max-width: 110rem;
    margin-inline: auto;
    line-height: 2.2rem;
    letter-spacing: .2rem;

    li {
        padding-bottom: .5rem;
    }

    .images {
        padding-block: 2rem;
        margin-inline: auto;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        gap: 2rem;
        max-width: 70rem;

        img {
            width: 100%;
        }
    }
`;