import { useEffect, useState } from "react";
import { Container } from "./style";
import BackButton from "../../backButton";

import OPEN from "./image/open.png";
import CLOSE from "./image/close.png";
import CELL1 from "./image/cell1.png";
import CELL2 from "./image/cell2.png";
import CELL3 from "./image/cell3.png";

const Menu = () => {
    const [handle, setHandle] = useState(false);
    var Width = window.screen.width;

    useEffect(()=>{
        if(Width >= 1000) {
            setHandle(true);
        }
    }, [Width]);

    return(
        <Container>
            <nav>
                <div className="topNav">
                    <h1>
                        Loja Dev
                    </h1>
                    <div className="menu" onClick={
                        ()=>{
                            handle ? setHandle(false) : setHandle(true);
                        }
                    }>
                        {!handle &&
                            <img className="open" src={OPEN} />
                        }
                        {handle &&
                            <img className="close" src={CLOSE} />
                        }
                    </div>
                </div>
                <ul className="itens" style={{
                    height: handle ? "30vh" : "0",
                }}>
                    <li className="item1" style={{
                        display: handle ? "block" : "none"
                    }}>
                        Início
                    </li>
                    <li className="item2" style={{
                        display: handle ? "block" : "none"
                    }}>
                        Produtos
                    </li>
                    <li className="item3" style={{
                        display: handle ? "block" : "none"
                    }}>
                        Promoções
                    </li>
                    <li className="item4" style={{
                        display: handle ? "block" : "none"
                    }}>
                        Contato
                    </li>
                </ul>
                <ul className="itens2">
                    <li className="item1" >
                        Início
                    </li>
                    <li className="item2" >
                        Produtos
                    </li>
                    <li className="item3" >
                        Promoções
                    </li>
                    <li className="item4" >
                        Contato
                    </li>
                </ul>
            </nav>
            <main>
                <div className="inicio">
                    <h2>
                        Seja bem vindo, nossos produtos são pensados para atender a todo tipo de gosto e idade. Aproveite!
                    </h2>
                    <div className="grid">
                        <div className="itensInicio">
                            <h3>Celular Rosa</h3>
                            <img src={CELL1} />
                            <p>Celular criado para jovens do público feminino jovem com todas as funcionalidades desejáveis para o dia a dia. Também temos a opção desse produto personalizado com as configurações pedidas pelo cliente.</p>
                            <button id="button1">
                                Comprar agora
                            </button>
                        </div>
                        <div className="itensInicio">
                            <h3>Celular Amarelo</h3>
                            <img src={CELL2} />
                            <p>Celular criado para jovens do público feminino jovem com todas as funcionalidades desejáveis para o dia a dia. Também temos a opção desse produto personalizado com as configurações pedidas pelo cliente.</p>
                            <button id="button2">
                                Comprar agora
                            </button>
                        </div>
                        <div className="itensInicio">
                            <h3>Celular Azul</h3>
                            <img src={CELL3} />
                            <p>Celular criado para jovens do público masculino jovem com todas as funcionalidades desejáveis para o dia a dia. Também temos a opção desse produto personalizado com as configurações pedidas pelo cliente.</p>
                            <button id="button3">
                                Comprar agora
                            </button>
                        </div>
                    </div>
                </div>
            </main>
            <BackButton />
        </Container>
    );
}

export default Menu;