import { Container } from "./style";
import BackButton from "../../backButton";

const Festival = () => {

    return(
        <Container>
            <div className="text">
                <h1>
                    Exercício de fixação
                </h1>
                <h3>
                    Festival do Rei
                </h3>
                <p>
                    Hoje é dia de festa, mas não para uma comemoração qualquer, e sim a comemoração mais alegre e esperada do ano, e eis o motivo,enfim recebemos a Carta do Rei.
                </p>
                <p>
                    Mas antes de iniciar as festividades iremos ler um trecho desse importante documento aclamado pelo povo desse reino.
                </p>
                <div id="carta">
                    <h4>
                        Carta do Rei
                    </h4>
                    <p>
                        Queridos súditos, é com grande alegria que venho vos informar através deste documento escrito que este ano conseguimos superar as nossas metas de abastecimento.
                    </p>
                    <p>
                        Nossas reservas de suprimentos nunca estiveram tão completas como estão nesse exato momento.
                    </p>
                    <p>
                        Após meses de planejamento e trabalho, conseguimos estocar o suficinete para ficarmos tranquilos durante nosso período de inverno.
                    </p>
                    <p>
                        E como já é de costume, iremos realizar o nosso festival de comemoração para 5 dias a contar da data em que escrevo este documento.
                    </p>
                    <p>
                        Mais uma vez quero demonstrar minha alegria com o esforço de todos e os espero de braços abertos para nosso incrível festival.
                    </p>
                    <p id="ass">
                        Ass.: Vosso rei.
                    </p>
                </div>
            </div>
            <BackButton />
        </Container>
    );
}

export default Festival;