import { useState } from "react";
import { Container } from "./styles";
import IMG1 from '../../assets/exemploJs.png';

const JS1 = () => {
    const [show, setShow] = useState(false);

    const open = () => {
        let box = document.querySelector('.box');

        if(show) {
            box?.classList.add('.none');
            box?.classList.add('back');
            setShow(false);
            
        } else {
            box?.classList.remove('.none');
            box?.classList.remove('back');
            setShow(true);
        }
    }

    return(
        <Container>
            <h3>O que é Javascript?</h3>
            <p>O JavaScript é uma das linguagens de programação mais conhecidas e antes de continuar precisamos entender que JavaScript e Java são duas linguagens diferentes (vale a pena dar uma estudada). Você já deve ter acessado uma página Web e recebido um sinal de alerta com alguma informação ou até mesmo foi alertado quando esqueceu de digitar uma informação em algum campo ou digitou de forma incorreta, então você já conheceu alguma função desenvolvida pelo JavaScript.</p>

            <h3>Algumas coisas que conseguimos fazer com o JavaScript.</h3>

            <ul>
                <li>Console.log - com esse comando conseguimos fazer alguns testes de forma que não apareça na tela para o usuário e sim no console do navegador que acessamos nas ferramentas, essa é uma ótima forma de verificar se estamos conseguindo fazer nosso código funcionar.</li>

                <li>Document.querySelector - com esse comando conseguimos acessar o primeiro item do documento HTML com aquela característica para fazermos alguma ação com ele. Também temos o querySelectorAll que seleciona todos os itens com aquela característica.</li>

                <li>Const - com a constante podemos definir um valor para ela, e ela é muito utilizada no em JS, quando definimos que a "const one = 1" então o valor de one será o número 1, então da próxima vez que formos precisar usar o 1 não precisamos digitar ele e sim a constante one. Com esse exemplo parece não fazer sentido, mas quando estamos fazendo o código isso pode nos ajudar muito.</li>

                <li>Let - tem uma funcionalidade parecida com a const, mas normalmente utilizamos quando precisamos de uma constante apenas dentro de uma função.</li>

                <li>Var - é uma variável com funcões parecidas com a constante, mas por conseguir alterar seu valor de forma mais fácil ela não é muito recomendada pelas boas práticas. Por exemplo, quando colocamos "var caixa = carro" e depois colocamos "var caixa = moto", o valor é alterado de carro para moto.</li>
            </ul>

            <h3>Quem criou o Javascript?</h3>
            <p>Com o nome inicial de LiveScript, a linguagem JavaScript foi criada por volta de 1995 por Brendan Eich a pedido da empresa Netscape. Mas a SUN Microsystems também ficou interessada no JS e começou a trabalhar no desenvolvimento da linguagem.</p>

            <p>Por a SUN Microsystems já manter a linguagem Java, esse fato fez com que a LiveScript tivesse seu nome alterado para JavaScript. Mas temos que lembrar que Java e JavaScript são linguagens tanto diferentes em seus conceito quanto em seus usos.</p>

            <h3>Agora vamos começar o nosso primeiro exemplo de código em CSS</h3>
            <p>Com o JavaScript vamos ver primeiro as imagens e depois veremos as explicações de cada passo feito.</p>

            <div className="images">
                <img src={IMG1} alt="Imagem com exemplo de código Javascript" />
            </div>

            <p>Primeiro vamos entender o que são esses "let", "const" e "var". O LET e o VAR são variáveis, conseguimos dar a ele valores de tags, por exemplo, para que não seja preciso estar digitando o processo pra acessar essa tag toda vez que for preciso. Com as variáveis podemos alterar seus valores, caso você dê um valor 'um' para uma variável e 10 linhas depois dê um valor 'dois' para a mesma let, era ficará com o valor 'dois' dentro dela. Com a CONST é diferente, o valor definido para uma constante não pode ser alterado após definido. Por as variáveis serem tão maleáveis, o seu uso deve ser muito restrito ou até mesmo evitado, pois além não ser considerada uma boa prática ela pode ter seu valor alterado de forma indevida e acabar estragando seu código.</p>

            <p>A forma mais aconselhável de utilizar a variável LET é para uma função específica, e ela podem ser criadas dentro da função e fora dela. Quando ela é criada fora da função ela pode ser chamada em mais de um local (escopo geral), mas se ela for criada dentro da função ela só terá valor dentro dessa referida função (escopo restrito).</p>

            <p>A CONST tem um uso parecido com a LET e a VAR, mas como explicado antes, ela não terá seu valor alterado depois que for definido, e a CONST é a mais aconselhável para uso em seu código, isso evitará que você acabe gerando bugs no seu código.</p>

            <p>Agora vamos aprender sobre nossas funções, como mostra na imagem, nossas funções são formadas da seguinte maneira: "function" é pra informar que temos a função, "mostrar()" é o nome da nossa função e dentro dos "{}" colocamos as ações que serão executadas quando a função for acionada.</p>

            <p>No nosso exemplo, as nossas funções irão executar ações para alterar a cor de fundo do main através das constantes e variáveis utilizadas e também iremos trocar os displays das classes none e function para que quando uma estiver visível a outra não aparecer.</p>

            <div className="main">
                <div className="box">
                    <h1>
                        Meu primeiro site
                    </h1>
                    <p>
                        Esse é meu primeiro exemplo de site e eu estou aprendendo com o Dev Para Todos.
                    </p>
                    <p>
                        Reza a lenda que para aprender de verdade como programar temos que utilizar a frase "Olá mundo!", caso você não a use em seu primeiro projeto a magia da programação não lhe aceitará e você não irá conseguir criar nenhum projeto.
                    </p>
                    <div>
                        <p>
                            Então para seguir a tradição passe o cursor do mouse por cima do quadrado e veja a mágica acontecer.
                        </p>
                        <div onClick={open}>
                            {show &&
                                <div id="caixa" >
                                    <div className="dot"  />
                                </div>
                            }
                            {!show &&
                                <div id="caixa" className="none" >
                                    <p>
                                        Olá mundo!
                                    </p>
                                </div>
                            }
                        </div>
                    </div>
                </div>
            </div>

            {/* <div className="images">
                <img src={IMG1} alt="imagem com exemplo de código HTML" />
                <img src={IMG2} alt="Imagem do navegador mostrando resultado do código HTML" />
            </div> */}

        </Container>
    );
}

export default JS1;