import { useState } from "react";
import { Container } from "./styles";
import LOGO from '../../assets/logo.png';
import MenuIcon from '@mui/icons-material/Menu';
import CloseIcon from '@mui/icons-material/Close';
import { Link } from 'react-router-dom';

const Header = () => {
    const [menu, setMenu] = useState(false);

    return(
        <Container>
            <div className="all">
                <div className="headerTop">
                    <div className="left">
                        <Link to='/site-devparatodos/'><img src={LOGO} alt="Logo DevParaTodos" /></Link>
                    </div>
                    <div className="right">
                        <div
                            className="menuIcon"
                            onClick={()=>{
                                menu ? setMenu(false) : setMenu(true);
                            }}
                        >
                            <div style={{ display: menu ? 'none' : 'block' }} >
                                <MenuIcon className="icon" />
                            </div>
                            <div style={{ display: menu ? 'block' : 'none' }} >
                                <CloseIcon className="icon" />
                            </div>
                        </div>
                    </div>
                </div>
                <div
                    style={{ 
                        height: menu ? '100vh' : '0',                     
                    }}
                    className="headerBottomMobile"
                >
                    <ul>
                        <li>
                            <Link className="link" to='/site-devparatodos/'>Início</Link>
                            <div className="line"/>
                        </li>
                        <li>
                            <Link className="link" to='/site-devparatodos/introduce'>Introdução</Link>
                            <div className="line"/>
                        </li>
                        <li>
                            <Link className="link" to='/site-devparatodos/html'>HTML</Link>
                            <div className="line"/>
                        </li>
                        <li>
                            <Link className="link" to='/site-devparatodos/css'>CSS</Link>
                            <div className="line"/>
                        </li>
                        <li>
                            <Link className="link" to='/site-devparatodos/javascript'>Javascript</Link>
                            <div className="line"/>
                        </li>
                        <li>
                            <Link className="link" to='/site-devparatodos/reactjs'>React</Link>
                            <div className="line"/>
                        </li>
                        {/* <li>
                            <Link className="link" to='/github'>Github</Link>
                            <div className="line"/>
                        </li> */}
                        <li>
                            <Link className="link" to='/site-devparatodos/exercicios'>Exercícios</Link>
                            <div className="line"/>
                        </li>
                    </ul>
                </div>
                <div
                    className="headerBottomWeb"
                >
                    <ul>
                        <li>
                            <Link className="link" to='/site-devparatodos/'>Início</Link>
                            <div className="line"/>
                        </li>
                        <li>
                            <Link className="link" to='/site-devparatodos/introduce'>Introdução</Link>
                            <div className="line"/>
                        </li>
                        <li>
                            <Link className="link" to='/site-devparatodos/html'>HTML</Link>
                            <div className="line"/>
                        </li>
                        <li>
                            <Link className="link" to='/site-devparatodos/css'>CSS</Link>
                            <div className="line"/>
                        </li>
                        <li>
                            <Link className="link" to='/site-devparatodos/javascript'>Javascript</Link>
                            <div className="line"/>
                        </li>
                        <li>
                            <Link className="link" to='/site-devparatodos/reactjs'>React</Link>
                            <div className="line"/>
                        </li>
                        {/* <li>
                            <Link className="link" to='/github'>Github</Link>
                            <div className="line"/>
                        </li> */}
                        <li>
                            <Link className="link" to='/site-devparatodos/exercicios'>Exercícios</Link>
                            <div className="line"/>
                        </li>
                    </ul>
                </div>
            </div>
        </Container>
    );
}

export default Header;