import { Container } from "./styles";
import IMG1 from '../../assets/js/templatestring/image1.png';
import IMG2 from '../../assets/js/templatestring/image2.png';
import { Video } from "../videos";

const TemplateString = () => {
    return(
        <Container>
            <h2>String X template string</h2>

            <h3>O que é uma template string?</h3>
            <p>Já sabemos que uma string é o tipo que damos aos textos, e a template string também é uma string, mas com algumas coisinhas a mais, veja nesse exemplo.</p>

            <div className="images">
                <img src={IMG1} alt="Imagem com exemplo de código Javascript" />
            </div>

            <p>No código acima temos duas variáveis com nome e sobrenome e uma terceira variável recebendo uma template string, veja que colocamos o "$" e abrimos e fechamos chaves para colocarmos o valor da variável dentro.</p>

            <h3>Outras formas de uso</h3>

            <p>Além de apenas imprimir o valor da variável, também podemos alterar na hora de usá-la, veja na imagem abaixo um exemplo com números.</p>

            <div className="images">
                <img src={IMG2} alt="Imagem com exemplo de código Javascript" />
            </div>

            <p>Ao dar o comando console.log você verá que uma variável que tem valor 50 e é somada à 5 terá uma impressão de 55. Aproveita e tenta fazer algumas operações como exercício de prática.</p>

            <h3>Assista essa aula para entender melhor</h3>

            <Video link='https://www.youtube.com/embed/fJQxP07Uak0' />

        </Container>
    );
}

export default TemplateString;