import BackButton from "../../backButton";
import { Container } from "./style";
import IMG1 from './assets/financas.jpg';
import IMG2 from './assets/saude.jpg';
import IMG3 from './assets/politica.jpg';
import IMG4 from './assets/desemprego.jpg';
import IMG5 from './assets/combustivel.jpg';
import IMG6 from './assets/esportes.jpg';

const Grid = () => {
    return(
        <Container>
            <header>
                <h1>Notícias do dia</h1>
                <h3>Tudo de primeira mão para você</h3>
            </header>
            <div className="quadros">
                <div className="quadro">
                    <img src={IMG1} />
                    <div>
                        <h3>Mercado financeiro</h3>
                        <p>Após crise mundial, Brasil se destaca com baixa inflação...</p>
                        <div className="btn">
                            <button id="btn1">Ler agora</button>
                            <button id="btn2">Salvar</button>
                        </div>
                    </div>
                </div>
                <div className="quadro">
                    <img src={IMG2} />
                    <div>
                        <h3>Saúde pública</h3>
                        <p>Em discurso durante evento anual da saúde, ministro da ...</p>
                        <div className="btn">
                            <button id="btn1">Ler agora</button>
                            <button id="btn2">Salvar</button>
                        </div>
                    </div>
                </div>
                <div className="quadro">
                    <img src={IMG3} />
                    <div>
                        <h3>Notícias políticas</h3>
                        <p>Tribunal Superior Eleitoral estima que 20% dos brasileiros...</p>
                        <div className="btn">
                            <button id="btn1">Ler agora</button>
                            <button id="btn2">Salvar</button>
                        </div>
                    </div>
                </div>
                <div className="quadro">
                    <img src={IMG4} />
                    <div>
                        <h3>Baixa no desemprego</h3>
                        <p>Após crise mundial, Brasil segura a menor taxa de desemprego...</p>
                        <div className="btn">
                            <button id="btn1">Ler agora</button>
                            <button id="btn2">Salvar</button>
                        </div>
                    </div>
                </div>
                <div className="quadro">
                    <img src={IMG5} />
                    <div>
                        <h3>Preço do combustível</h3>
                        <p>Baixa histórica no preço do combustível começa a ser vista...</p>
                        <div className="btn">
                            <button id="btn1">Ler agora</button>
                            <button id="btn2">Salvar</button>
                        </div>
                    </div>
                </div>
                <div className="quadro">
                    <img src={IMG6} />
                    <div>
                        <h3>Notícias esportivas</h3>
                        <p>Seleção brasileira se prepara para o próximo amistoso contra...</p>
                        <div className="btn">
                            <button id="btn1">Ler agora</button>
                            <button id="btn2">Salvar</button>
                        </div>
                    </div>
                </div>
            </div>
            <BackButton />
        </Container>
    );
}

export default Grid;