import { Container } from "./styles";
import { Video } from "../videos";

const Objeto2 = () => {
    const abrir = "{";
    const fechar = "}";

    return(
        <Container>
            <h2>Alterando informações de objetos</h2>

            <p>Já aprendemos que objetos são um grupo de informações, agora vamos aprender a inserir ou alterar essas informações. Let's go.</p>

            <p>Vamos continuar com o mesmo exemplo da outra aula e vamos alterar o nome para Miguel através do seguinte comando: pessoa.nome = 'Miguel';</p>

            <p>Agora vamos adicionar 20 anos à idade com o comando: pessoa.idade += 20;</p>

            <p>E também adicionar uma nova profissão com: pessoa.profissao.push('pai');</p>

            <p>Use o "console.log(pessoa)" e veja as alterações o console do navegador.</p>

            <p>Agora que já sabemos manipular algumas informações dos objetos, vamos ver um modelo de objeto mais complexo e aprender a acessar as informações dele.</p>

            <div className="function">
                <p>
                    <span>let pessoa = {abrir}</span><br />
                    <span className="spanSpace">nome: 'João',</span><br />
                    <span className="spanSpace">idade: 55,</span><br />
                    <span className="spanSpace">profissao: [</span><br />
                    <span className="spanSpace2">{abrir} periodo: 'manhã', cargo: 'professor' {fechar},</span><br />
                    <span className="spanSpace2">{abrir} periodo: 'tarde', cargo: 'empreendedor' {fechar}</span><br />
                    <span className="spanSpace">],</span><br />
                    <span className="spanSpace">ativo: true,</span><br />
                    <span className="spanSpace">filhos: {abrir}</span><br />
                    <span className="spanSpace2">filho1: 'Maria',</span><br />
                    <span className="spanSpace2">filho2: 'José'</span><br />
                    <span className="spanSpace">{fechar}</span><br />
                    {fechar}
                </p>
            </div>

            <p>Veja que na profissão adicionamos dois objetos dentro de um array e para acessar a informação do cargo da primeira profissão usaremos o comando: console.log(pessoa.profissao[0].cargo);</p>

            <p>Por as profissões estarem dentro de um array devemos utilizar as chaves ([]) para acessar os dados.</p>

            <p>Legal que você tenha chegado até aqui, aproveita e faz um exemplo diferente e tenta acessar as informações dele. Te vejo na próxima aula.</p>

            {/* <h3>Assista essa aula para entender melhor</h3>

            <Video link='https://www.youtube.com/embed/E-p5NoFjFOo' /> */}

        </Container>
    );
}

export default Objeto2;