import { Container } from "./styles";

import IMG1 from '../../assets/exemploHtml.png';
import IMG2 from '../../assets/navegadorHtml.png';

const HTML1 = () => {
    return(
        <Container>
            <h3>O que é HTML?</h3>
            <p>A sigla HTML significa HyperText Markup Language (Linguagem de Marcação de HiperTexto), e sua versão mais atualizada é o HTML5. Para explicar de uma forma mais fácil de entender, nós utilizamos o HTML para inserir conteúdo em uma página web e esse conteúdo tem um sentido. A intenção é inserir o conteúdo em uma organização através de tags e esse conteúdo não aparece com uma aparência na qual estamos acostumados no dia a dia, mas temos que lembrar que a intenção é inserir o conteúdo e não a estilização, esse é o papel do CSS que será o nosso próximo tópico. Para ficar melhor de associar, pensaremos no HTML como um corpo, o CSS como as roupas e os movimentos do corpo como o JavaScript (que também veremos logo mais).</p>

            <h3>Algumas tags HTML</h3>
            <p>Agora veremos algumas tags HTML, lembrando que não são todas e que não é regra ter que decorar todas de início, com o passar do tempo e com a prática você irá memorizar todas as mais utilizadas nos seus projetos. Cada tag tem em sua constituição um código que define qual será a sua função dentro do documento, por exempo a tag "p" que defique um parágrafo.</p>

            <ul>
                <li>h1 - essa é uma tag de título, sendo a tag de título mais forte. Em cada página devemos utilizar apenas uma tag h1. Em seguida temos h2, h3, h4, h5 e h6. Cada uma delas tem um grau de importância, sendo em ordem decrescente.</li>

                <li>p - é a tag para criar parágrafos.</li>

                <li>ul - tag utilizada para listas desordenadas.</li>

                <li>ol - tag para criar listas ordenadas.</li>

                <li>li - tag que insere os elementos de uma lista. Cada li é um elemento.</li>

                <li>button - tag utilizada para inserir um botão no código.</li>

                <li>img - para inserir uma imagem.</li>

                <li>video - insere vídeos ao código.</li>

                <li>script - essa tag é usada para inserir JavaScript ao código, podendo fazer no mesmo arquivo. Mas o mais aconselhado é criar um arquivo novo apenas para o JS.</li>

                <li>link - com essa tag podemos inserir um link de para uma página com os arquivos CSS, inserir um favicon e outras funções.</li>
            </ul>

            <h3>Quem criou o HTML?</h3>
            <p>Tim Berners-Lee é o nome dele, o cara que criou o nosso querido HTML. Em 1991 o HTML foi criado para facilitar o envio de documentos por instituições de pesquisa e em 1992 ele foi liberado para uso mundial na internet com a biblioteca World Wide Web que conhecemos como WWW.</p>

            <h3>Para quê serve o HTML e como utilizar?</h3>
            <p>Com o HTML podemos criar documentos de páginas web, podemos criar e inserir os conteúdos de uma página com ele. Lembrando que com ele todo conteúdo inserido deve ter um sentido e não apenas por simples aparência, a parte de estilização fica por conta do CSS por exemplo. Para criar códigos com HTML só precisamos ter um editor de código instalado em nosso computador. Tendo como exemplo o Visual Studio Code mais conhecido como VSCode. Ao realizar a instalação do mesmo nós temos as opções de criar arquivos ".html" ".css" e ".js".</p>
            
            <h3>Agora vamos começar o nosso primeiro exemplo de código em HTML.</h3>

            <p>Vamos entender aos poucos como funciona a estrutura do nosso documento HTML. Veja que ele inicia dizendo que é um documento HTML, após isso temos o idioma que o documento está escrito e após isso temos o head.

            Veja que dentro do head temos o charset="UTF-8", que significa praticamente o seguinte: o idioma utilizado nesse documento faz uso de palavras com acentos e cedilha. Pois o idioma português faz uso desses itens.

            Depois vemos o title que é o título que irá aparecer na guia em seu navegador. Por exemplo, aqui o título é Meu primeiro site.

            Após a tag head temos a tag body que na tradução significa corpo, e é isso que ela é, o corpo do nosso documento. Dentro do body temos a tag main e dentro dela iremos inserir o nosso conteúdo. Caso Tivéssemos um rodapé iríamos inserir uma tag footer logo após o fechamento da tag main. Mas nesse exercício não iremos utilizar um footer.

            Abrimos a tag "h1" com o intuito de dizer que esse é o título central do corpo do documento, esse é o título principal. Lembrando que cada página só se deve utilizar um título "h1", mas de "h2" até "h6" podemos utilizar quantos forem necessários.

            A tag "p" inserida significa que temos um parágrafo de texto digitado. Digamos que seria um parágrafo de texto de um livro ou de um jornal.

            A tag "div" utilizada significa em termos menos técnicos que estamos abrindo uma caixa para inserir mais conteúdo dentro dela, isso irá facilitar nosso trabalho principalmente na hora em que estiver-mos estilizando nosso documento ou inserindo funções de programação com o JS.

            Agora veja como fica o documento digitado no editor de código e como aparece no navegador. Lembrando que aqui estamos apresentando o HTML puro. Para ver ele estilizado continue na explicação do CSS.</p>

            <div className="images">
                <img src={IMG1} alt="imagem com exemplo de código HTML" />
                <img src={IMG2} alt="Imagem do navegador mostrando resultado do código HTML" />
            </div>

        </Container>
    );
}

export default HTML1;