import { Container } from "./styles";
import { Video } from "../videos";

const LoopFor = () => {
    const abrir = "{";
    const fechar = "}";
    const maiorque = ">";
    const menorque = "<";

    return(
        <Container>
            <h2>Loop For</h2>

            <h3>O que é um loop?</h3>
            <p>Um loop é quando você quer executar um código mais de uma vez. Na programação é muito comum fazer o uso dos loops para obter algum resultado, independente que qual linguagem seja.</p>

            <p>Vamos ver um exemplo de um loop for simples como um contador que irá executar um console.log 10 vezes.</p>

            <div className="function">
                <p>
                    <span>for(let n = 1; n {menorque} 11; n++) {abrir}</span><br />
                    <span className="spanSpace">console.log('usuário ' + n);</span><br />
                    <span>{fechar}</span>
                </p>
            </div>

            <p>No exemplo acima estamos utilizando o loop for que diz o seguinte, criou a variável "n" e enquanto o valor de "n" for menor que 10 será somado 1 e executada função do console.log. Então irá aparecer 10 mensagens (usuário 0 até usuário 9).</p>            

            <p>Veja que a contagem foi a partir de "usuário 0", caso você queira fazer a contagem iniciar de "usuário 1" será necessário que n comece com 1 (n = 1). Fazendo isso nós teremos nosso loop executado nove vezes, e para que ele volte a ser executado dez vezes indo até o "usuário 10" você pode adicionar 11 ao segundo parâmetro (n {menorque} 11).</p>

            <p>Na próxima aula iremos usar um loop em um array, mas antes disso você pode tentar criar alguns loops para aprender mais como eles funcionam. Até a próxima.</p>

            {/* <h3>Assista essa aula para entender melhor</h3>

            <Video link='https://www.youtube.com/embed/E-p5NoFjFOo' /> */}

        </Container>
    );
}

export default LoopFor;