import { Container } from "./styles";
import IMG1 from '../../assets/projetojs1img1.jpg';
import IMG2 from '../../assets/projetojs1img2.jpg';
import { Video } from "../videos";

const Variaveis = () => {
    return(
        <Container>
            <h2>Variáveis e constantes (var, let, const)</h2>

            <h3>O que é uma variável/constante?</h3>
            <p>Assim como na matemática temos o uso das letras como variáveis que server para calcular os valores que elas significam, na programação elas são criadas e um valor é definido para elas.</p>

            <p>Ao criar uma variável x e definir-mos um valor 10 a ela (var x = 10) então essa variável vale pelo number 10. E por ser uma "var" nós podemos redefinir seu valor posteriormente dando outro valor a ela como o number 50 por exemplo (x = 50).</p>

            <p>Existem as variáveis "let" e "var" e também a constante "const". Para um primeiro contato você precisa apenas entender que as variáveis podem ter seus valores alterados e a constante não.</p>

            <h3>Qual a diferença entre Let e Var?</h3>

            <p>Normalmente quando usamos uma "var" nós a utilizamos fora de funções e quando utilizamos a "let" é para um valor de uso específico em uma função ou algo parecido.</p>

            <h3>Tipos de dados recebidos em variáveis e constantes</h3>

            <p>Os tipos são: number, string, boolean, undefined, NaN, null, array e object. No fim desse texto deixamos um vídeo explicando detalhadamente.</p>

            <h3>Assista essa aula para entender melhor</h3>

            <Video link='https://www.youtube.com/embed/RG9PdKO1hDw' />

        </Container>
    );
}

export default Variaveis;