import { Container } from "./styles";
import { Video } from "../videos";

const Array2 = () => {
    return(
        <Container>
            <h2>Operações com arrays - length, push, pop, shift</h2>

            <h3>O que podemos fazer com os arrays?</h3>
            <p>Lembra que falamos que um array pode ser iniciado vazio e depois você pode inserir dados? Pois é, além de inserir dados vamos te ensinar agora a ver quantos itens o array tem e a como excluir o primeiro e último item do array. Let's go.</p>

            <p>Abaixo temos um array com quatro valores, e para ter acesso a quantidade no console do navegador vamos utilizar o comando console.log(paises.length), o length é o comando para saber quantos itens você tem dentro do array, mesmo que ele seja enorme você poderá ter acesso a essa informação com o length().</p>

            <p className="function">let paises = ['Brasil', 'Peru', 'Bolívia', 'Argentina'];</p>

            <p>Para inserir um valor vamos usar o paises.push('Equador'), assim iremos adicionar 'Equador' à última posição ([4]) do nosso array.</p>

            <p>Com o "paises.pop()" conseguimos excluir o último item do nosso array, nesse caso, se seguir a ordem iremos excluir o adicionado anteriormente (Equador).</p>

            <p>Já com "paises.shift()" iremos excluir o primeiro item do nosso array, nesse caso o 'Brasil'.</p>

            <p>Veja que esses métodos agem como funções, então sempre lembre que eles necessitam dos parênteses para funcionarem.</p>

            <div className="function">
                <p>paises.length() - diz quantos itens temos dentro do array</p>
                <p>paises.push() - insere um item ao array</p>
                <p>paises.pop() - remove o último item do array</p>
                <p>paises.shift() - remove o primeiro item do array</p>
            </div>

            <p>Com o passar do tempo você verá outras coisas legais que conseguimos fazer com arrays, mas não se preocupe se não conseguir decorar todas, o importante é entender o que está acontecendo para que quando você precisar fazer essas ações acima você se lembre que esses métodos existem para lhe ajudar.</p>

            <p>Aproveita e faz alguns exemplos para aprender cada vez mais. Te vejo na próxima aula.</p>

            {/* <h3>Assista essa aula para entender melhor</h3>

            <Video link='https://www.youtube.com/embed/E-p5NoFjFOo' /> */}

        </Container>
    );
}

export default Array2;