import { Container } from "./styles";
import { Link } from 'react-router-dom';
import LOGO from '../../assets/logo.png';
import InstagramIcon from '@mui/icons-material/Instagram';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import GitHubIcon from '@mui/icons-material/GitHub';
import { Email } from "../Email";

const Footer = () => {
    return(
        <Container>
            <div className="footer">
                <div className="footer1">
                    <div className="img">
                        <img src={LOGO} alt="Logo DevParaTodos" />
                    </div>
                    <p>Criado por <Link className="social" to='https://www.instagram.com/codewithluan/' target={"_blank"}>Luan Silva</Link></p>
                    <p>Todos os direitos reservados</p>
                    <div className="icons">
                    <Link className="social" to='https://www.instagram.com/codewithluan/' target={"_blank"}>
                        <InstagramIcon className="icon"/>
                    </Link>
                    <Link className="social" to='https://www.linkedin.com/in/luan-silva-b7262a235/' target={"_blank"}>
                        <LinkedInIcon className="icon"/>
                    </Link>
                    <Link className="social" to='https://github.com/LuanGitSilva' target={"_blank"}>
                        <GitHubIcon className="icon"/>
                    </Link>
                    </div>
                </div>
                {/* <div className="footer2">
                    <Email />
                </div> */}
                <div className="footer3">
                    <ul>
                        <li>
                            <Link className="link" to='/site-devparatodos/'>Início</Link>
                            <div className="line"/>
                        </li>
                        <li>
                            <Link className="link" to='/site-devparatodos/introduce'>Introdução</Link>
                            <div className="line"/>
                        </li>
                        <li>
                            <Link className="link" to='/site-devparatodos/html'>HTML</Link>
                            <div className="line"/>
                        </li>
                        <li>
                            <Link className="link" to='/site-devparatodos/css'>CSS</Link>
                            <div className="line"/>
                        </li>
                        <li>
                            <Link className="link" to='/site-devparatodos/javascript'>Javascript</Link>
                            <div className="line"/>
                        </li>
                        <li>
                            <Link className="link" to='/site-devparatodos/reactjs'>React</Link>
                            <div className="line"/>
                        </li>
                        {/* <li>
                            <Link className="link" to='/github'>Github</Link>
                            <div className="line"/>
                        </li> */}
                        <li>
                            <Link className="link" to='/site-devparatodos/exercicios'>Exercícios</Link>
                            <div className="line"/>
                        </li>
                    </ul>
                </div>
            </div>
        </Container>
    );
}

export default Footer;