import { Container } from "./styles";
import { Video } from "../videos";

const Objeto = () => {
    const abrir = "{";
    const fechar = "}";

    return(
        <Container>
            <h2>Objeto</h2>

            <h3>O que é objeto?</h3>
            <p>É um grupo de informações em que veremos propriedades e características. Assim como um array, um objeto nos ajuda a armazenar informações ou nos fornece essas informações.</p>

            <p>Vamos ver agora como seria um objeto com informações sobre uma pessoa.</p>

            <div className="function">
                <p><span>let pessoa = [</span><br />
                      <span className="spanSpace">nome: 'João',</span><br />
                      <span className="spanSpace">idade: 55,</span><br />
                      <span className="spanSpace">profissao: 'professor'</span><br />
                      <span className="spanSpace">ativo: true</span><br />
                ]</p>
            </div>

            <p>Criamos uma variável que recebeu o objeto com as informações, e assim como podemos ter array dentro de array, podemos ter objeto dentro de objeto, veja o exemplo abaixo.</p>

            <div className="function">
                <p>
                    <span>let pessoa = {abrir}</span><br />
                    <span className="spanSpace">nome: 'João',</span><br />
                    <span className="spanSpace">idade: 55,</span><br />
                    <span className="spanSpace">profissao: 'professor'</span><br />
                    <span className="spanSpace">ativo: true</span><br />
                    <span className="spanSpace">filhos: {abrir}</span><br />
                    <span className="spanSpace2">filho1: 'Maria',</span><br />
                    <span className="spanSpace2">filho2: 'José'</span><br />
                    <span className="spanSpace">{fechar}</span><br />
                    {fechar}
                </p>
            </div>

            <p>Também podemos ter arrays dentro de objetos.</p>

            <div className="function">
                <p>
                    <span>let pessoa = {abrir}</span><br />
                    <span className="spanSpace">nome: 'João',</span><br />
                    <span className="spanSpace">idade: 55,</span><br />
                    <span className="spanSpace">profissao: ['professor', 'empreendedor'],</span><br />
                    <span className="spanSpace">ativo: true,</span><br />
                    <span className="spanSpace">filhos: {abrir}</span><br />
                    <span className="spanSpace2">filho1: 'Maria',</span><br />
                    <span className="spanSpace2">filho2: 'José'</span><br />
                    <span className="spanSpace">{fechar}</span><br />
                    {fechar}
                </p>
            </div>

            <p>Agora vamos ver como acessar as informações desse objeto, com o "console.log(pessoa.nome)" temos acesso ao nome, para acessar o nome do primeiro filho iremos usar console.log(pessoa.filhos.filho1), e para acessar a segunda profissão que está no array usaremos console.log(pessoa.profissao[1]).</p>

            <p>Pronto, você já conhece os objetos, tenta criar alguns exemplos com suas informações e de pessoas que você conheça, até porque é praticando que se aprende. Até a próxima aula.</p>

            {/* <h3>Assista essa aula para entender melhor</h3>

            <Video link='https://www.youtube.com/embed/E-p5NoFjFOo' /> */}

        </Container>
    );
}

export default Objeto;