import { Container } from "./styles";

import IMG1 from '../../assets/explicacss01.png';
import IMG2 from '../../assets/explicacss02.png';
import IMG3 from '../../assets/exemploCss.png';
import IMG4 from '../../assets/navegadorCss.png';

const CSS1 = () => {
    return(
        <Container>
            <h3>O que é CSS?</h3>
            <p>Agora vamos ter uma breve noção de CSS (Cascading Style Sheets). E antes de iniciar vamos esclarecer que tanto o HTML quanto o CSS não são considerados linguagens de programação, mas com a grande evolução de configurações do CSS já existem alguns pensamentos de comparação entre algumas funções do CSS e do JavaScript. Como mensionado antes, uma forma mais fácil de entender o CSS é saber que ele é apenas para estilização do documento, com ele podemos adicionar cores, alterar o tamanho do conteúdo inserido, escolher a ordem e a posição de cada item, entre muitas outras coisas. A forma mais organizada de usar o CSS é criando um arquivo .css separado do arquivo .htlm (essas são terminações utilizadas no editor de código para dizer qual o conteúdo do arquivo). No final da tag head inserimos uma tag link para ligar o arquivo .html em uso ao arquivo .css que estamos criando para estilizar o documento. E assim, se não houver um arquivo .css já criado, o editor dará a opção de criar um e já o deixa conectado. Dentro das nossas tag HTML podemos inserir algumas tags CSS, como por exemplo a class e o id, utilizamos essas tags para chamar a configuração da tag HTML no arquivo .css e alterá-la. Já no arquivo .css podemos inserir, por exemplo, um background-color para mudar a cor de fundo, um color para alterar a cor da letra caso seja um parágrafo, um height para definir a altura, width para definir a largura, font-size/font-weight/font-family para alterações nas letras. Esses são apenas alguns dos muitos comandos que iremos aprender.</p>

            <h3>Algumas tags CSS</h3>
            <ul>
                <li>Font-size - configura o tamanho da fonte.</li>

                <li>Font-weight - configura o peso da fonte, normalmente uma fonte de letra tem vários tamanhos e pesos, temos o bold que é o negrito e o bolder que é um negrito mais forte dizendo de uma forma mais leiga.</li>

                <li>Font-family - configura a fonte que será utilizada, dependendo de como a utilizamos podemos configurar a fonte de uma palavra ou de todo o site.</li>

                <li>Height - configura a altura do item selecionado, caso se defina a altura de uma div por exemplo de 150px ela aparecerá com essa configuração na tela.</li>

                <li>Width - configura a largura do item selecionado, caso se defina a largura de uma div por exemplo de 150px ela aparecerá com essa configuração na tela.</li>

                <li>Cursor - define a forma que o cursor irá aparecer quando ele estiver em cima do item, por exemplo o "cursor: pointer" que deixará o cursor em forma de "mão" o que normalmente indica que este item é clicável.</li>

                <li>Padding - o padding é um preenchimento que inserimos ao item selecionado, podemos usar o padding para inserir em todos os lados, padding block para inserir na parte de cima e de baixo, padding inline para inserir nos lados esquedo e direito, e também podemos usar em um lado específio como padding-top, padding-bottom, padding-left, padding-right.</li>

                <li>Border - utilizada para inserir uma borda no item selecionado, com ele conseguimos definir a largura da borda, a cor, se ela será contínua ou pontilhada, entre outras coisas.</li>

                <li>Border-radius - com ele podemos definir uma forma arredondada na borda e também podemos escolher qual ponto da borda será arredondado. Por exemplo se colocar "border-radius: 1px 2px 3px 4px" o lado superior esquerdo terá um arredondamento de 1px, o superior direito de 2px, o inferior direito de 3px e o inferior esquerdo de 4px.</li>

                <li>Text-decoration - com essa tag conseguimos realizar algumas configurações nos textos, como por exemplo o "text-decoration: underline" que deixa o texto selecionado sublinhado.</li>
            </ul>

            <h3>Quem criou o CSS?</h3>
            <p>Em 1994, Håkon Wium Lie convidou Bert Bos para criarem o projeto do CSS (Cascading Style Sheets). Em 1995 ele apresentaram o projeto e a W3C (World Wide Web Consortium) ficou interessado pelo plano e croiu uma equipe liderada por Håkon Wium Lie e Bert Bos para continuar o desenvolvimento do projeto que veio a ser lançado em 1996 sua primeira versão.</p>

            <h3>Para quê serve o CSS e como utilizar?</h3>
            <p>O CSS não é uma linguagem de programação mas sim uma forma de estilização do documento que é apresentado na tela. Quando criamos um documento em HTML ele fica praticamente um bloco abaixo do outro, sem nenhum tipo de estilização. Antes era possível realizar algumas estilizações com o HTML, mas isso mudou e vem ficando cada vez mais fora de uso, pois o HTML é um documento que inserimos conteúdo com sentido e não apenas por estilo, a parte de estilização nós deixamos com o CSS. Para utilizar o CSS nós criamos um arquivo ".css" e ligamos ele com um link ao documento ".html". Também podemos fazer a estilização inline, colocando cada código CSS dentro das tags HTML, mas essa não é uma técnica bem vista pelas boas práticas. Também podemos abrir uma tag style no "head" no HTML e inserir o CSS. Mas também não é recomendado pelas boas práticas da programação.</p>
            
            <h3>Dando continuidade, vamos ver de fato a estilização do CSS funcionando.</h3>

            <p>Antes de iniciar, vamos ver uma coisa interessante que faz parte do HTML, mas que devemos saber ao usar o CSS.

            Veja abaixo que quando inserimos uma tag no HTML ela oculpa toda uma caixa que vai do lado esquerdo da tela ao lado direito, e se apenas inserir-mos o conteúdo um após o outro eles irão ficar um abaixo do outro e todos eles ocupam toda uma caixa. Você verá com o passar do tempo que o entendimento sobre isso fará sentido quando estiver colocando itens um ao lado do outro, já que para fazer isso teremos que alterar a configuração da caixa em que a tag HTML está inserida.

            A primeira imagem retrada o que foi explicado anteriormente na tela do navegador. E a segunda imagem é o que normalmente vemos quando abrimos o inspecionar do navegador para conseguirmos enxergar melhor nosso documento no navegador. Para abrir o inspecionar é só clicar com o botão direito do mouse e clicar em inspecionar.</p>

            <div className="images">
                <img src={IMG1} alt="imagem com exemplo de código css" />
                <img src={IMG2} alt="Imagem do navegador mostrando resultado do código css" />
            </div>

            <h3>Explicando a utilização do "ID" e da "CLASS"</h3>

            <p>Primeiro vamos entender que estamos criando um arquivo ".css" e temos que vincular ele ao arquivo ".html", temos que conectar eles através de um link. Então vamos criar o arquivo "style.css" e criar o link rel="stylesheet" href="exemploCss.css" para conseguirmos realizar a estilização do documento.

            Agora com o arquivo ".css" criado vamos entender o conceito de "id" e "class", todos os dois são métodos que podemos utilizar para realizar a estilização. Vamos entender a diferença entre os dois.

            ID - inserindo um "id" na nossa tag "html" nós conseguimos chamar essa tag no documento ".css" através de um "#". Por exemplo, pegamos uma tag p e colocamos um id= texto, agora vamos no arquivo ".css" e chamamos essa tag como #texto{} veja que abrimos e fechamos o "{}", é dentro deles que iremos inserir as configurações css para estilização. Mas o "ID" só poderá ser utilizado em uma única tag HTML, então não conseguimos inserir o mesmo ID em 3 tags por exemplo.

            CLASS - já com o "class" nós identificamos a tag HTML assim class= textos e no documento ".css" chamamos ela como ".textos", veja que para a "class" utilizilamos o "." na parte CSS. E diferente do ID, com a CLASS você poderá utilizar essa class em quantas tags HTML quiser.</p>

            <h3>Agora vamos começar o nosso primeiro exemplo de código em CSS</h3>

            <p>Já com nosso arquivo ".css" criado e feito o link dele no arquivo ".html" vamo começar nossas configurações. A primeira será utilizando o " * " que aqui significa uma configuração global do arquivo, iremos inserir:</p>

            <ul>
                <li>padding: 0; para que inicialmente todo o padding (preenchimento) das tags fiquem zeradas.</li>
                <li>border: 0; para que as bordas fiquem zeradas.</li>
                <li>margin: 0; para que as margens fiquem zeradas.</li>
                <li>box-sizing: border-box; aqui estamos dizendo que independente do que tivez na tag, o tamanho dela será contado do final das bordas.</li>
            </ul>

            <p>Agora para nosso body vamos usar:</p>

            <ul>
                <li>width: 100vw; para que o body fique com toda a largura da nossa tela, esse "vw" significa viewport width = largura da tela em termos mais leigos.</li>
                <li>height: 100vh; aqui também temos uma configuração de viewport, mas essa é viewport height, ou seja, altura da tela.</li>
                <li>display: flex; o display flex nos permite manusear os itens dentro da nossa tag, por exemplo, se quisermos posicionar os itens desse body em colunas devemos inserir um display flex e um flex-direction collumn, mas como queremos que eles fiquem um abaixo do outros iremos não iremos dizer que quermos em linhas pois não precisa, mas para deixar em linhas utilizamos o flex-direction row. O display flex é bem mais complexo que isso, então recomendamos uma leitura mais aprofundada sobre ele.</li>
                <li>align-items: center; com esse é como se passassemos uma linha ao centro tag e dissessemos que tudo ficará alinhado de acordo com essa linha, nem irá exceder para baixo, nem para cima, isso porque estamos com nosso display em forma de linha, então essa linha imaginária será horizontal.</li>
                <li>justify-content: center; com esse deixaremos os itens dentro do body ao centro dele, e como estamos utilizando display em forma de linhas, o justify-content trata o conteúdo como coluna e irá igualar ao meio da tela o conteúdo.</li>
                <li>background-color: rgb(235, 232, 232); com essa configuração estamos inserindo uma cor de fundo, e nesse caso é uma cor quase branca, ela tem um leve escurecimento para ajudar na visualização do usuário, pois cores muito claras ou vívidas podem acabar nos ofuscando durante a vizualização e causando sensação de desconforto.</li>
            </ul>

            <p>Para o nosso main vamos iserir:</p>

            <ul>
                <li>width: 350px; aqui estamos inserindo uma largura, veja que ela está em píxels.</li>
                <li>padding: 20px; com esse preenchimento nós evitamos que o conteúdo dentro do main fique próximo demais das bordas ou até mesmo que encoste, assim com o padding teremos algo mais organizado.</li>
                <li>background-color: rgb(84, 187, 120); essa cor de fundo é uma cor esverdeada para destacar o main do body.</li>
                <li>display: flex; o display flex como explicado anteriormente nos ajuda a escolher o direcionamento do conteúdo, e aqui iremos usar o direcionamento de coluna como mostra a configuração a seguir.</li>
                <li>flex-direction: column;</li>
                <li>justify-content: center;</li>
                <li>gap: 20px; esse gap é praticamente um disanciamento entre os itens.</li>
                <li>box-shadow: 0px 0px 6px .5px; aqui estamos inserindo uma sombra ao redor para dar uma sensação de que o main está um pouco elevado do body e por isso fica o sombreamento ao redor, devido a elevação.</li>
            </ul>

            <p>Para o nosso título h1 e para nossas tags p:</p>

            <ul>
                <li>text-align: center; faz com que os textos fiquem centralizados.</li>
            </ul>

            <p>Agora vamos utilizar um id, e lembrando que no css referenciamos ele com uma #.</p>
            
            <ul>
                <li>
                #caixa {
                    <p>padding: 20px;</p>
                }
                </li>
            </ul>

            <p>Agora temos o uso de uma class, lembrando que referenciamos ela no css com um " . ".</p>

            <ul>
                <li>
                .function {
                    <><p>display: block; com esse block, diferentemente do flex, ele deixará o conteúdo fixo onde quer que ele esteja.</p><p>margin: auto; aqui estamos dizendo que queremos que seja inserida uma margem automáica para deixar nosso conteúdo centralizado, então o navegador irá deixar a margem maleável para ele conseguir centralizar.</p><p>border: 1px solid; com essa configuração estamos inserindo uma borda ao redor do conteúdo, podemos escolher a largura, com esse sólido estamos dizendo que ela será contínua e sem detalhes, também poderíamos ter inserido uma cor, mas não foi necessário.</p><p>width: 5px;</p><p>height: 5px;</p><p>background-color: black;</p><p>cursor: pointer; aqui estamos dizendo o seguinte, quando o cursor estiver por cima do conteúdo é para aparecer a mãozinha como se indicasse alguma coisa diferente naquele local.</p></>
                }
                </li>
            </ul>

            <p>Essa próxima agora é uma configuração de algo que ficará escondido na tela e sóaparecerá quando o cursor estiver sobre o ponto preto na tela.</p>

            <ul>
                    
                <li>
                        .none {
                            <><p>display: none;</p><p>margin: auto;</p><p>width: 110px;</p><p>background-color: aliceblue;</p><p>font-size: 20px;</p><p>box-shadow: 0px 0px 3px 0.5px;</p><p>border-radius: 20px; o border radius nos permite adicionar uma característica arredondada ao conteúdo.</p><p>cursor: pointer;</p></>
                        }
                </li>
            </ul>

            <h3>Agora veja como fica no editor de código e o resultado no navegador</h3>

            <div className="images">
                <img src={IMG3} alt="imagem com exemplo de código css" />
                <img src={IMG4} alt="Imagem do navegador mostrando resultado do código css" />
            </div>

        </Container>
    );
}

export default CSS1;