import { Container } from "./styles";
import { Video } from "../videos";

const Dom = () => {
    return(
        <Container>
            <h2>DOM</h2>

            <h3>O que significa DOM?</h3>
            <p>Document Object Model (modelo de objeto de documento).</p>

            <p>É a representação de dados dos objetos que compõem a estrutura e o conteúdo de um documento na Web. Ou seja, o DOM é que faz o conteúdo renderizado aparecer na tela.</p>

            <p>Para que nosso projeto rode na web ele precisa estar em html, por isso, independente da linguagem que você usa o conteúdo que aparece no navegador é em html.</p>

            <p>Para entender melhor, quando você estudou html você aprendeu que o arquivo fica em forma de árvore, ou seja, ele tem tags nomeadas e elas tem seu grau de importância.</p>

            <p>A partir de agora vamos aprender mais sobre o DOM e iremos aprender a manipulá-lo, trocando informações, inserindo ou excluindo.</p>

            {/* <h3>Assista essa aula para entender melhor</h3>

            <Video link='https://www.youtube.com/embed/E-p5NoFjFOo' /> */}

        </Container>
    );
}

export default Dom;