import { Container2 } from "./styles";
import { useState } from "react";
import CSS1 from "./css1";
import ProjetoCss1 from "./projeto1";
import ProjetoCss2 from "./projeto2";

const CSSContent = () => {
    const [html1, setCss1] = useState(false);
    const [projeto1, setProjeto1] = useState(false);
    const [projeto2, setProjeto2] = useState(false);

    const show1 = () => {
        setCss1(true);
        setProjeto1(false);
        setProjeto2(false);
    }
    const show2 = () => {
        setCss1(false);
        setProjeto1(true);
        setProjeto2(false);
    }

    const show3 = () => {
        setCss1(false);
        setProjeto1(false);
        setProjeto2(true);
    }

    return(
        <Container2>
            <h2>Escolha um tema</h2>
            <ul>
                <li onClick={show1}>Introdução</li>
                <li onClick={show2}>Projeto 1</li>
                <li onClick={show3}>Projeto 2</li>
            </ul>

            {html1 && <CSS1 />}
            {projeto1 && <ProjetoCss1 />}
            {projeto2 && <ProjetoCss2 />}
            
        </Container2>
    );
}

export default CSSContent;