import { Container } from "./styles";
import IMG1 from '../../assets/js/ifelse/image1.jpg';
import IMG2 from '../../assets/js/ifelse/image2.jpg';
import IMG3 from '../../assets/js/ifelse/image3.jpg';
import { Video } from "../videos";

const IfElse = () => {
    return(
        <Container>
            <h2>If else</h2>

            <h3>O que é o If Else?</h3>
            <p>O if else é a nossa porta de entrada para lógica na programação do Javascript. Ele é uma forma de darmos comandos em forma de código para que ações sejam executadas dependendo de informações que sejam apresentadas. Para ficar melhor de entender vamos deixar a seguir uma imagem de um exemplo do uso do if else para você.</p>

            <div className="images">
                <img src={IMG1} alt="Imagem com exemplo de código Javascript" />
            </div>

            <p>No código acima temos duas variáveis com nome e idade. Após elas temos nosso "if()", dentro do parêntese iremos dizer a condição que nesse caso é nome ser igual a string José, caso seja, o código entre chaves será executado, caso não seja, o código do else (se não) será executado.</p>

            <p>Veja na imagem abaixo um exemplo em que o "if" não será compatível com a condição e o "else" será executado.</p>

            <div className="images">
                <img src={IMG2} alt="Imagem com exemplo de código Javascript" />
            </div>

            <h3>Criando If dentro de If</h3>

            <p>Além de fazer como nos exemplos anteriores, também podemos criar "if" dentro de "if", mas não muitos, pois se muitos forem criados pode acarretar em confusão para entender o código. Veja na imagem abaixo dois exemplos, um com dois "if" e outro com três.</p>

            <div className="images">
                <img src={IMG3} alt="Imagem com exemplo de código Javascript" />
            </div>

            <p>Em um código real, o if else nos ajuda muito a filtrar informações e até mesmo liberar acesso para pessoas autorizadas e esconder informações que não devam ser mostradas.</p>

            <h3>Atenção!</h3>

            <p>É sempre bom que você vá fazendo o código e testando no seu computador, assim você irá se familiarizar cada vez mais com o que for estudando.</p>

            <h3>Assista essa aula para entender melhor</h3>

            <Video link='https://www.youtube.com/embed/v9iQIYMuQGs' />

        </Container>
    );
}

export default IfElse;