import styled from "styled-components";

export const Container = styled.div`
    background-color: var(--headerColor);
    width: 100%;
    
    .footer {
        margin-inline: auto;
        max-width: 130rem;
        display: flex;
        flex-direction: column-reverse;
        justify-content: center;
        align-items: center;
        gap: 3rem;
        padding: 10rem 2rem;
        color: var(--borderColor);
        font-size: 2rem;
    }

    .footer1 {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 1rem;

        .social {
            text-decoration: none;
            color: var(--borderColor);

            :hover {
                color: var(--color3);
            }
        }

        .img {
            width: 13rem;
            margin-bottom: 2rem;
            
            img {
                width: 100%;
                height: 5.5rem;
                cursor: pointer;
            }
        }

        p {
            margin: 0;
            text-align: center;
        }

        .icons {
            display: flex;
            gap: 1rem;
            padding: 3rem;

            .icon {
                width: 2.7rem;
                height: 2.7rem;
                cursor: pointer;
            }
        }
    }

    .footer2 {
        width: 100%;
    }

    .footer3 {
        ul {
            display: flex;
            flex-direction: column;
            align-items: end;
            gap: 2rem;
            padding: 0;

            li {
                width: 9.5rem;
                list-style: none;
                text-align: center;
                cursor: pointer;

                .link {
                    color: var(--borderColor);
                    text-decoration: none;
        
                    :hover {
                        color: var(--color3);
                    }
                }
        
                .line {
                    background-color: var(--color3);
                    height: 1px;
                    width: 0;
                    transition: ease-in-out .5s;
                }
                
                :hover {        
                    .line {
                        width: 100%;
                    }
                }
            }
        }

    }

    @media screen and (min-width: 768px) {
        .footer {
            display: grid;
            grid-template-columns: 1fr 1fr;
            align-items: flex-start;
            gap: 2%;
        }

        .footer3{
            ul {
                margin: 0;
                align-items: center;

                li {
                    text-align: center;
                }
            }
        }
    }
`;