import styled from "styled-components";

export const Container = styled.div`
    padding: 30px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;


    .text {
        @media screen and (min-width: 800px) {
            max-width: 70rem;
        }
        
        text-align: center;
        margin-bottom: 5rem;

        h1,
        h3,
        h4 {
            display: flex;
            justify-content: center;
        }
    
        h1 {
            font-size: 26px;
        }
    
        h3 {
            font-size: 22px;
        }
    
        h4 {
            font-size: 20px;
        }
    
        p {
            font-size: 16px;
            text-align: justify;
            text-indent: 30px;
        }
        
    }

    p {
        font-size: 16px;
    }

    a {
        text-decoration: none;
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 2rem auto 0 auto;
        background-color: chocolate;
        width: 5rem;
        height: 1.8rem;
        color: white;
        border-radius: 2.5rem;
        transition: .4s;
    }

    a:hover {
        text-decoration: underline;
        transform: scale(1.1);
    }


    #carta {
        background-color: rgb(244, 212, 190);
        padding: 15px;
        border: 1px solid grey;
        border-top-left-radius: 50px;
        border-bottom-right-radius: 50px;
        font-family: monospace;
    }

    #ass {
        display: flex;
        justify-content: end;
    }

    .box {
        max-width: 35rem;
        display: flex;
        flex-direction: column;
        align-items: center;

        h2 {
            font-size: 3rem;
            color: var(--headerColor);
        }
    }

    .image {
        width: 100%;
        border: 1px solid var(--borderColor);
        border-radius: 2rem;
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;

        img {
            width: 100%;
            border-radius: 2rem;
            cursor: pointer;
        }
    }
`;