import { Container } from "./styles";
import IMG1 from '../../assets/js/switch/image1.jpg';
import IMG2 from '../../assets/js/switch/image2.jpg';
import { Video } from "../videos";

const Switch = () => {
    return(
        <Container>
            <h2>Switch</h2>

            <h3>O que é um Switch</h3>
            <p>Assim como o if else funciona com condições, o switch também trabalha da mesma forma, mas o switch pode agir com muito mais informação que um simples if else. Para entender melhor veja abaixo uma imagem com um código em if else e outra com um código feito em switch e os dois tratam do mesmo resultado.</p>

            <div className="images">
                <img src={IMG1} alt="Imagem com exemplo de código Javascript" />
            </div>

            <p>Nesse código temos três variáveis criadas, mas apenas a primeira está ativa, veja que no switch passamos profession por um tipo de filtro e vamos criando os cases que dizem o seguinte, se profession tiver o valor de 'fiscal' execute isso aqui e pare (break), se for 'bombeiro' execute isso e pare, se for 'policial' execute isso e pare, se não for nenhuma das opções anteriores execute isso aqui e pare.</p>

            <p>É importante colocar o "break" pois se não for colocado ele executará a order e continuará na próxima linha.</p>

            <p>Agora veja o código com if else.</p>

            <div className="images">
                <img src={IMG2} alt="Imagem com exemplo de código Javascript" />
            </div>

            <p>Em um primeiro contato parece que o if else é a melhor opção, mas acredite em mim, o switch é uma prática muito mais bem vista que o if else quando se trata de muitas condições em uma mesma questão, e também facilita muito em uma manutenção futura no código.</p>

            <p>Para melhor absorver o conteúdo tente fazer alguns exemplos você mesmo, crie alguns switch e if else para ir praticando.</p>

            <h3>Assista essa aula para entender melhor</h3>

            <Video link='https://www.youtube.com/embed/-DrstaFQAAA' />

        </Container>
    );
}

export default Switch;