import { Container } from "./styles";
import { Link } from "react-router-dom";
import IMG1 from '../../assets/html/projeto2/projeto2img1.jpg';
import IMG2 from '../../assets/html/projeto2/projeto2img2.jpg';
import IMG3 from '../../assets/html/projeto2/projeto2img1-1.jpg';
import IMG4 from '../../assets/html/projeto2/projeto2img1-2.jpg';

const Projeto2 = () => {
    return(
        <Container>
            <h2>Projeto 2</h2>

            <h3>Pesquisa de satisfação</h3>
            <p>Nesse projeto iremos utilizar HTML, CSS e Javascript, por isso será preciso ter o conhecimento básico sobre essas tecnologias para entender a lógica do projeto. E para uma prática ainda mais legal, vamos fazer um projeto de duas páginas. Let's go!</p>

            <p>Na primeira imagem temos o conteúdo HTML da primeira página (index.html), com uma estrutura de criação básica criada através do atalho "!".</p>

            <p>Dentro da tag "body" iniciamos com uma div que contém uma imagem dentro. Por esse projeto ser inspirado nos desafios do site frontend mentor as imagens foram do site deles, mas você pode baixar suas próprias imagens para uso. Não esqueça da class estrela na div, pois com ela iremos fazer a estilização.</p>

            <p>Vamos ter uma atenção especial na div de class btns, nela temos nossos botões de notas. Cada botão tem uma class e um id, não esqueça de colocá-los, pois cada um tem sua função. Cada botão está acionando a função clicou() e enviando um parâmetro relacionado ao texto, nesse caso a nota.</p>

            <p>A próxima div está com outro botão, esse é o que chama a função confirmar que permite ir para a próxima página após a votação.</p>

            <p>Não esqueça de adicionar a tag "script" relacionando seu documento HTML com o Javascript.</p>

            <div className="images">
                <img src={IMG1} alt="imagem com exemplo de código HTML" />
            </div>

            <p>O próximo passo é fazer a página 2 (index2.html), essa será a página de agradecimento por o cliente ter feito a votação.</p>

            <p>Mais uma vez, não se esqueça de adicionar a class e id das tags, pois a estilização depende deles.</p>

            <div className="images">
                <img src={IMG2} alt="imagem com exemplo de código HTML" />
            </div>

            <p>Veja abaixo como ficaram as duas páginas.</p>

            <div className="images">
                <img src={IMG3} alt="imagem com exemplo de código HTML" />
            </div>  

            <div className="images">
                <img src={IMG4} alt="imagem com exemplo de código HTML" />
            </div>        

            <p>Para continuar esse projeto vá para a <Link to='/site-devparatodos/css'>página CSS</Link>. Te vejo lá!</p>

        </Container>
    );
}

export default Projeto2;