import { Container } from "./styles";
import { Video } from "../videos";

const EventoClique = () => {
    const texto1 = '<button onclick="clicar()">clicar</button>';
    const texto2 = 'const clicar = () => {';
    const texto3 = 'console.log("Botão clicado!");';
    const texto4 = '}';

    return(
        <Container>
            <h2>Evento de clique</h2>

            <h3>O que é isso?</h3>
            <p>Dentre vários eventos que acontecem, temos o evento de clique que usamos para acionar uma função. Normalmente usado em botões, o evento de clique é utilizado no documento HTML ao adicionarmos a tag do botão da seguinte forma: {texto1}. Assim não só estamos criando a tag, mas também dizendo que quando o botão for clicado a função "clicar" será ativada.</p>

            <p>Abaixo temos um exemplo da função "clicar" feita no documento ".js" em que a ação executada pela função será um console.log.</p>

            <div className="function">
                <p>
                    <span>{texto2}</span><br />
                    <span className="spanSpace">{texto3}</span><br />
                    <span>{texto4}</span>
                </p>
            </div>

            <p>Existem vários outros eventos que acontecem quando estamos navegando em uma página, como por exemplo o "onclick" pode ser comparado à uma junção entre o "pressdown" e o "pressup", mas com certeza o evento mais utilizado pelo Javasccript é o "onclick".</p>

            <p>Para se familiarizar mais com o "onclick" você pode tentar criar mais botões que acionam funções, afinal é com a prática que aprendemos cada vez mais. Te vejo na próxima aula.</p>

            {/* <h3>Assista essa aula para entender melhor</h3>

            <Video link='https://www.youtube.com/embed/E-p5NoFjFOo' /> */}

        </Container>
    );
}

export default EventoClique;