import { Container } from "./styles";
import { Video } from "../videos";

const ManipulandoDom = () => {
    const texto1 = '<h1 id="titulo">Título principal</h1>';
    const texto2 = '<button onclick="clicar()">clicar</button>';
    const texto3 = 'function clicar () {';
    const texto4 = "document.getElementById('titulo').innerHTML = 'Novo título'";
    const texto5 = "}";
    const texto6 = "document.getElementById('titulo').innerText = 'Novo título';";

    return(
        <Container>
            <h2>Manipulando o DOM</h2>

            <p>A essa altura do campeonato você já aprendeu a criar seu documento HTML e também alguns passos do Javascript, agora vamos aprender a manipular o conteúdo que temos no HTML, o que chamamos de manipulação do DOM.</p>

            <p>Uma das formas que podemos fazer isso é com o "innerHTML", com esse comando estamos alterando o conteúdo interno do elemento.</p>

            <p>Vamos fazer a manipulação de um título do html ({texto1}) através do clique em um botão ({texto2}) com a função abaixo que troca de 'Título principal' para 'Novo título'.</p>

            <div className="function">
                <p>
                    <span>{texto3}</span><br />
                    <span className="spanSpace">{texto4}</span><br />
                    <span>{texto5}</span>
                </p>
            </div>

            <p>Já com o "innerText" alteramos o texto de algum elemento que o contenha, pode ser um h1, h2, h3, h4, h5, h6, p, li, ou outros que tenham texto. Mesmo sendo parecidos, o innerHTML é mais utilizado que é innerText, mas você precisa conhecer os dois, pois na programação cada coisa tem sua utilizade.</p>

            <div className="function">
                <p>
                    <span>{texto3}</span><br />
                    <span className="spanSpace">{texto6}</span><br />
                    <span>{texto5}</span>
                </p>
            </div>

            <p>Agora que você conhece o innerHTML e o innerText aproveita e faz alguns exemplos, nos vemos na próxima aula.</p>

            {/* <h3>Assista essa aula para entender melhor</h3>

            <Video link='https://www.youtube.com/embed/E-p5NoFjFOo' /> */}

        </Container>
    );
}

export default ManipulandoDom;