import styled from "styled-components";

export const Container2 = styled.div`
    margin-top: var(--navHeight);
    padding: 5rem 2rem;
    font-size: 2rem;
    text-align: center;
    max-width: 110rem;
    margin-inline: auto;
    line-height: 2.2rem;
    letter-spacing: .2rem;  

    h2 {
        margin-bottom: 5rem;
        text-align: center;
    }

    ul {
        margin-inline: auto;
        padding: 2rem;
        border: 2px solid var(--bgColorHover);
        border-radius: 1rem;
        display: flex;
        flex-direction: column;
        align-items: start;
        justify-content: center;
        gap: .5rem;
    }
    
    li {
        width: fit-content;
        list-style: inside;
        cursor: pointer;
        font-size: 1.6rem;
        text-align: start;

        :hover {
            text-decoration: underline;
        }
    }

    .spanSpace {
        padding-left: 2rem;
    }

    .spanSpace2 {
        padding-left: 4rem;
    }

    .spanSpace3 {
        padding-left: 6rem;
    }

    .spanSpace4 {
        padding-left: 8rem;
    }

    .function {
        border: 2px solid var(--bgColorHover);
        border-radius: 1rem;
        padding: 1rem;
    }
`;

export const Container = styled.div`
    margin-top: var(--navHeight);
    padding: 5rem 2rem;
    font-size: 1.7rem;
    text-align: justify;
    max-width: 110rem;
    margin-inline: auto;
    line-height: 2.2rem;
    letter-spacing: .2rem;

    a {
        text-decoration: none;
        color: var(--headerColor);

        :hover {
            text-decoration: underline;
        }
    }

    li {
        padding-bottom: .5rem;
    }

    .images {
        padding-block: 2rem;
        width: 100%;
        display: flex;
        flex-direction: column;
        gap: 2rem;

        img {
            width: 100%;
        }
    }

    .main {
        background-color: var(--borderColor);
        padding-block: 5rem;
        padding-inline: 1rem;
        margin-top: 5rem;
    }

    .box {
        margin-inline: auto;
        max-width: 35rem;
        padding: 20px;
        border-radius: 2rem;
        background-color: rgb(84, 187, 120);
        display: flex;
        flex-direction: column;
        justify-content: center;
        gap: 20px;
        box-shadow: 0px 0px 6px .5px;

        h1,
        p {
            text-align: center;
        }

        #caixa {
            padding: 20px;           
        }

    }

    .back {
        background-color: #f1f1f1;
    }

    .dot {
        width: 2rem;
        height: 2rem;
        border-radius: 5rem;
        background-color: #000;
        margin-inline: auto;
        cursor: pointer;
    }

    .none {
        margin: auto;
        height: 2rem;
        width: 110px;
        font-size: 20px;
        box-shadow: 0px 0px 3px 0.5px;
        border-radius: 20px;
        cursor: pointer;
        background-color: yellow;

        p {
            margin-block: 0;
        }
    }

    @media screen and (min-width: 900px) {
        .images {
            flex-direction: row;
        }  
    }
`;