import { Link } from "react-router-dom";
import { Container } from "./style";

import FestivalIMG from '../../assets/festival.jpeg';
import ProductIMG from '../../assets/roupa.png';
import MenuIMG from './menu/image/menu.jpg';
import GridIMG from './grid/assets/grid.jpg';
import ParalaxIMG from './paralax/page.jpg';

const Exercicios = () => {
    return(
        <Container>
            <div className="box">
                <h2>Exercício 1</h2>
                <p>Exercício utilizando HTML e CSS básico de forma responsiva. Tente fazer o exercício de forma que o texto se encaixe na tela.</p>
                <Link to='/site-devparatodos/exercicios/festival'>
                    <div className="image" >
                        <img src={FestivalIMG} />
                    </div>
                </Link>
            </div>
            <div className="box">
                <h2>Exercício 2</h2>
                <p>Exercício criando um mostruário de item com um botão para adicionar um produto ao carrinho de compras.</p>
                <Link to='/site-devparatodos/exercicios/product'>
                    <div className="image" >
                        <img src={ProductIMG} />
                    </div>
                </Link>
            </div>
            <div className="box">
                <h2>Exercício 3</h2>
                <p>Neste exercício tente criar um mostruário com três itens de venda de forma responsiva e com um menu para quando estiver em modo mobile.</p>
                <Link to='/site-devparatodos/exercicios/menu'>
                    <div className="image" >
                        <img src={MenuIMG} />
                    </div>
                </Link>
            </div>
            <div className="box">
                <h2>Exercício 4</h2>
                <p>Neste exercício tente criar os itens utilizando especificamente o display grid para deixar o projeto responsivo.</p>
                <Link to='/site-devparatodos/exercicios/grid'>
                    <div className="image" >
                        <img src={GridIMG} />
                    </div>
                </Link>
            </div>
            <div className="box">
                <h2>Exercício 5</h2>
                <p>Neste exercício tente criar os itens utilizando especificamente o efeito parallax nas imagens, de forma que quando houver o scroll o texto fique sobreposto a elas.</p>
                <Link to='/site-devparatodos/exercicios/paralax'>
                    <div className="image" >
                        <img src={ParalaxIMG} />
                    </div>
                </Link>
            </div>
        </Container>
    );
}

export default Exercicios;