import { Container } from "./style";
import IMG1 from '../../../assets/roupa.png';
import AddShoppingCartIcon from '@mui/icons-material/AddShoppingCart';
import BackButton from "../../backButton";

const Product = () => {

    return(
        <Container>
            <div className="text">
                <div className="img">
                    <img src={IMG1} alt="Foto do conjunto oferado" />
                </div>
                <div className="descricao">
                    <h3>Conjuntos de inverno</h3>
                    <h1>Conjunto moda inverno kid feminino</h1>
                    <p>4 peças com modelagem criada para dar conforto com estilo ao público kid feminino. O material utilizado é 100% ecológico conforme nossas diretrizes.</p>
                    <div className="price">
                        <p>De</p>
                        <h4>R$149,00</h4>
                        <p>Por</p>
                        <h2>R$99,00</h2>
                    </div>
                    <button>
                        <AddShoppingCartIcon
                            style={{
                                width: '3rem',
                                height: '3rem'
                            }}
                        />
                        Adicionar ao carrinho
                    </button>
                </div>
            </div>
            <BackButton />
        </Container>
    );
}

export default Product;