import { Container } from './style';
import Typewriter from "typewriter-effect";
import { useNavigate } from 'react-router-dom';

export const NotFoundComponent = () => {
    const navigate = useNavigate();

    const handleBackButton = () => {
        navigate(-1);
    }

    return (
        <Container>
            <div className='notFound'>
                <div>4</div>
                <div className='zero'>
                    <div className='circleLeft'>
                        <div className='ball ballLeft' />
                    </div>
                    <div className='circleRight'>
                        <div className='ball ballRight' />
                    </div>
                </div>
                <div>4</div>
            </div>

            <div className='message'>
                <Typewriter
                    options={{
                       loop: true
                    }} 
                    onInit={(typewriter)=> {
                        typewriter    
                        .typeString("Página não encontrada!")
                        .pauseFor(1000)
                        .deleteAll()
                        .typeString("Tente novamente!")
                        .pauseFor(1000)
                        .start()
                        
                    }}
                />
            </div>

            <button
                className='btn'
                onClick={handleBackButton}>
                Voltar
            </button>
        </Container>
    );
} 