import { Container } from './styles';
import html from '../../assets/html.png';
import css from '../../assets/css.png';
import javascript from '../../assets/js.png';
import react from '../../assets/react.png';
import { Link } from "react-router-dom";


const Intro = () => {
    return(
        <Container>
            <div className="all">
                <div className="text">
                    <h3>Projeto social</h3>
                    <h1>DevParaTodos</h1>
                    <h3>
                        Ensinando programação web de forma gratuita para todos.
                    </h3>
                    <Link className="btn" to="/site-devparatodos/introduce">Iniciar</Link>
                </div>
                <div className="logos">
                    <img className="img1" src={html} alt="logo do html" />
                    <img className="img2" src={css} alt="logo do css" />
                    <img className="img3" src={javascript} alt="logo do javascript" />
                    <img className="img4" src={react} alt="logo do react" />
                </div>
            </div>
        </Container>
    );
}

export default Intro;