import { Container2 } from "./styles";
import { useState } from "react";
import HTML1 from "./html1";
import Projeto1 from "./projeto1";
import Projeto2 from "./projeto2";

const HTMLContent = () => {
    const [html1, setHtml1] = useState(false);
    const [projeto1, setProjeto1] = useState(false);
    const [projeto2, setProjeto2] = useState(false);

    const show1 = () => {
        setHtml1(true);
        setProjeto1(false);
        setProjeto2(false);
    }
    const show2 = () => {
        setHtml1(false);
        setProjeto1(true);
        setProjeto2(false);
    }
    const show3 = () => {
        setHtml1(false);
        setProjeto1(false);
        setProjeto2(true);
    }

    return(
        <Container2>
            <h2>Escolha um tema</h2>
            <ul>
                <li onClick={show1}>Introdução</li>
                <li onClick={show2}>Projeto 1</li>
                <li onClick={show3}>Projeto 2</li>
            </ul>

            {html1 && <HTML1 />}
            {projeto1 && <Projeto1 />}
            {projeto2 && <Projeto2 />}
            
        </Container2>
    );
}

export default HTMLContent;