import { Container } from "./styles";
import { Video } from "../videos";

const Objeto3 = () => {
    const abrir = "{";
    const fechar = "}";
    const nome = "${this.nome}";
    const sobrenome = "${this.sobrenome}";

    return(
        <Container>
            <h2>Funções em objetos</h2>

            <p>Agora vamos aprender a usar função dentro de um objeto, e no nosso exemplo vamos criar um objeto com nome e sobrenome e usar uma função para criar um nome completo juntando os nomes.</p>

            <div className="function">
                <p>
                    <span>let pessoa = {abrir}</span><br />
                    <span className="spanSpace">nome: 'João',</span><br />
                    <span className="spanSpace">sobrenome: 'Miguel',</span><br />
                    <span className="spanSpace">idade: 55,</span><br />
                    <span className="spanSpace">nomeCompleto: function() {abrir}</span><br />
                    <span className="spanSpace2">return this.nome + ' ' + this.sobrenome;</span><br />
                    <span className="spanSpace4">ou</span><br />
                    <span className="spanSpace2">return `{nome} {sobrenome}`;</span><br />
                    <span className="spanSpace">{fechar}</span><br />
                    {fechar}
                </p>
            </div>

            <p>O "this" nesta function é usado como se fosse o próprio objeto, é uma ótima forma de não repetir o nome do objeto. Se usar arrow function não tem como usar o this para ter acesso ao objeto.</p>

            <p>Para ver o resultado vamos usar o comando: "console.log(pessoa.nomeCompleto())";</p>

            <p>Veja que estamos usando nomeCompleto como se estivesse chamando uma função, pois se não usar os parênteses o que será impresso é a forma que a função é feita e não o resultado.</p>

            <p>Você já percorreu um bom caminho até aqui, te espero na próxima aula.</p>

            {/* <h3>Assista essa aula para entender melhor</h3>

            <Video link='https://www.youtube.com/embed/E-p5NoFjFOo' /> */}

        </Container>
    );
}

export default Objeto3;