import { Container } from "./styles";
import { Link } from "react-router-dom";
import IMG1 from '../../assets/html/projeto2/projeto2img3.jpg';
import IMG2 from '../../assets/html/projeto2/projeto2img4.jpg';
import IMG3 from '../../assets/html/projeto2/projeto2img5.jpg';
import IMG4 from '../../assets/html/projeto2/projeto2img1-3.jpg';
import IMG5 from '../../assets/html/projeto2/projeto2img1-4.jpg';

const ProjetoCss2 = () => {
    return(
        <Container>
            <h2>Projeto 2</h2>

            <h3>Iniciando estilização</h3>

            <p>Continuando o projeto 2, agora vamos iniciar a estilização. Como no projeto anterior, iriei destacar as configurações mais interessantes no momento. Então, let's go!</p>

            <p>Para deixar nosso projeto tomando conta de toda a tela, começamos com height e width 100%, display flex, align-itens e justify-content center, assim o conteúdo ficará no meio da página mesmo que o tamanho da tela mude.</p>

            <p>Algo para se destacar é a font-family que escolhemos a Arial. Veja que ela está da seguinte forma: "Arial, Helvetica, sans-serif", isso significa que primeiro o navegador irá tentar usar a Arial, caso não consiga ele irá tentar usar a Helvetica, e se mesmo assim não conseguir ele usará qualquer fonte sans-serif. Fontes sans-serif são as fontes que não tem aqueles detalhes pontudos, já a fonte times new rome é uma fonte com serif.</p>

            <p>Nosso conteúdo será uma caixa com width de 300px e height de 300px, mas como estamos adicionando padding ele ficará um pouco maior, nesse caso 60px maior em largura e altura pois o padding está sendo adicionado em todos os lados.</p>

            <div className="images">
                <img src={IMG1} alt="imagem com exemplo de código CSS" />
            </div>

            <p>Algo que também vamos destacar é a estilização da class "btnClicked", essa class será adicionada ao botão de nota clicado através do Javascript, então não esqueça desse detalhe.</p>

            <p>Veja que a class ".sub" está com display: none, isso significa que ela não fica amostra no navegador, mas logo depois temos uma class ".subDisplay", essa class será ativada quando o cliente escolher a nota, fazendo assim com que o botão de "Enviar" fique visível e clicável.</p>

            <div className="images">
                <img src={IMG2} alt="imagem com exemplo de código CSS" />
            </div>

            <p>Também estamos fazendo o uso do ":hover" nas classes ".btn:hover e ..sub button:hover", isso fará com que os botões tenham uma cor de destaque quando o cursor estiver sobre eles.</p>

            <div className="images">
                <img src={IMG3} alt="imagem com exemplo de código CSS" />
            </div>

            <p>Essa foi a nossa estilização do projeto, veja agora as imagens de como as páginas ficam após o CSS.</p>

            <div className="images">
                <img src={IMG4} alt="imagem com exemplo de código CSS" />
            </div>

            <div className="images">
                <img src={IMG5} alt="imagem com exemplo de código CSS" />
            </div>

            <p>Agora que você já viu o HTML e CSS do projeto continue com o <Link to='/site-devparatodos/javascript'>Javascript</Link> e veja a lógica que iremos usar para fazer os botões funcionarem. Te vejo lá!</p>

        </Container>
    );
}

export default ProjetoCss2;