import { Container } from "./styles";
import { Video } from "../videos";

const ElementosDom = () => {
    const texto1 = "<h1>Título principal</h1>";
    const texto2 = '<p id="texto1">texto 1</p>';
    const texto3 = '<p class="texto2">texto 2</p>';

    return(
        <Container>
            <h2>Selecionando elementos do DOM</h2>

            <h3>Como fazemos isso?</h3>
            <p>document - é o documento que está sendo renderizado na tela.</p>

            <p>Agora nós vamos aprender a selecionar os elementos pelo nome da tag, pelo id e pela class.</p>

            <h3>GetElementsByTagName</h3>

            <p>Digamos que você tenha em seu documento HTML um "{texto1}", vamos selecionar ele pela tag utilizando document.getElementsByTagName("h1"), pronto, selecionamos o h1. Para poder trabalhar com ele, seja fazendo alterações ou adicionar conteúdo você irá precisar colocar ele em uma variável, então pode fazer "let titulo = document.getElementsByTagName("h1")".</p>

            <h3>GetElementById</h3>

            <p>Agora vamos selecionar um elemento através do id que ele tenha, digamos que você tenha o elemento "{texto2}", para selecioná-lo iremos usar "document.getElementById("texto1")", agora você o terá selecionado. E para usar uma variável você pode fazer como foi feito no exemplo anterior (let texto1 = document.getElementById("texto1")).</p>

            <h3>GetElementsByClassName</h3>

            <p>A próxima maneira de selecionar um elemento é pela class que você der a ele, vamos usar o exemplo "{texto3}", e para selecioná-lo iremos usar "document.getElementsByClassName", e assim você terá selecionado o elemento através da class, podendo colocá-lo em uma variável também.</p>

            <h3>QuerySelector</h3>

            <p>Sem dúvidas o seletor mais fácil de se usar é o Query Selector, com ele conseguimos selecionar pelo id, pela class, pela tag name, e também conseguimos selecionar o que está dento das tags.</p>

            <p>Vamos fazer três exemplos para você entender melhor.</p>

            <p>Nesse primeiro estamos selecionando através do id, assim como já feito com o "getElementById".</p>

            <p>document.querySelector("#texto1");</p>

            <p>Nesse outro estamos selecionando através da class, assim como no "getElementByClassName".</p>

            <p>document.querySelector(".lista");</p>

            <p>E nesse terceiro estamos fazendo um efeito cascata em que acessamos a class lista, depois a "ul" que está dentro dela, e por fim a "li".</p>

            <p>document.querySelector(".lista ul li");</p>

            <p>Também podemos utilizar o "querySelectorAll" que seleciona todos os elementos com aquelas características. Se você tiver vários "li" para selecionar, o querySelectorAll será ideal para isso.</p>

            <p>document.querySelectorAll("li");</p>

            <p>Logo mais você verá como esse assunto é importante, mas até lá tenta recriar os exemplos feitos aqui trocando as informações. Te vejo na próxima aula.</p>

            {/* <h3>Assista essa aula para entender melhor</h3>

            <Video link='https://www.youtube.com/embed/E-p5NoFjFOo' /> */}

        </Container>
    );
}

export default ElementosDom;