import styled from "styled-components";

export const Container = styled.div`
    padding: 10rem 4rem;
    display: flex;
    flex-direction: column;
    max-width: 100rem;
    margin-inline: auto;

    h2 {
        font-size: 3rem;
        text-align: center;
        margin: 5rem 0 10rem 0;
        color: var(--color2);

        span {
            font-size: 3.5rem;
            color: var(--headerColor);
        }
    }

    .box {
        border: 10px solid var(--bgColorHover);
        padding: 2rem;
        border-radius: 3rem;
    }

    .MyAwesomeParallax {        
        @media screen and (min-width: 425px) {
            border: 1px solid var(--borderColor);   
            border-radius: 3rem;
            padding: 5rem;
            margin-bottom: -5rem;
            margin-right: -5rem;
        }
    }
    
    .subjects {
        display: grid;
        gap: 5rem;
        max-width: 40rem;
        margin-inline: auto;

        @media screen and (min-width: 900px) {
            grid-template-columns: 1fr 1fr;
            max-width: 80rem;
        }
        @media screen and (min-width: 1200px) {
            grid-template-columns: 1fr 1fr;
            max-width: 115rem;
        }
    }

    .link {
        text-decoration: none;
        color: var(--color2);
    }

    .subject {
        width: 100%;
        display: flex;
        gap: 2rem;
        cursor: pointer;
        transition: ease-in-out .3s;
        border-radius: 2rem;

        :hover {
            background-color: var(--bgColorHover);

            .square img {
                animation: go-top 1s infinite alternate;
            }
        }
    }

    .square {
        border: 1px solid var(--borderColor);
        border-radius: 2rem;
        width: 10rem;
        padding: 1rem;

        img {
            width: 100%;
            height: 100%;
        }
    }

    .turn img {
        animation: circle 5s linear infinite;
    }

    .text {
        display: flex;
        flex-direction: column;
        justify-content: center;
        gap: 1rem;
        align-items: center;
        margin-inline: auto;

        h3 {
            font-size: 2rem;
            margin: 0;
        }

        p {
            font-size: 1.7rem;
            margin: 0;
            text-align: center;
        }
    }

    @keyframes circle {
        100% {
            transform: rotate(360deg);
        }
    }

    @media screen and (min-width: 1024px) {
        flex-direction: row-reverse;
        justify-content: center;
        align-items: center;
        gap: 5rem;
    }
`;