import styled from "styled-components";

export const Container = styled.div`
    max-width: 130rem;
    margin-inline: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 10rem;

    .box {
        max-width: 35rem;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: 4rem;

        h2 {
            font-size: 3rem;
            color: var(--headerColor);
        }
    }

    .image {
        width: 100%;
        border: 1px solid var(--borderColor);
        border-radius: 2rem;
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;

        img {
            width: 100%;
            border-radius: 2rem;
            cursor: pointer;
        }
    }

    p {
        font-size: 16px;
    }

    @media screen and (min-width: 800px) {
        display: grid;
        grid-template-columns: 1fr 1fr;
        max-width: 100rem;

        .box {
            margin-inline: auto;
        }
    }

    @media screen and (min-width: 1200px) {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        max-width: 130rem;

        .box {
            margin-inline: auto;
        }
    }
`;