import { Container2 } from "./styles";
import { useState } from "react";
import ReactJS1 from "./reactjs1";
import Projeto1 from "./projeto1";

const ReactjsContent = () => {
    const [reactjs1, setReactjs1] = useState(false);
    const [projeto1, setProjeto1] = useState(false);

    const show1 = () => {
        setReactjs1(true);
        setProjeto1(false);
    }
    const show2 = () => {
        setReactjs1(false);
        setProjeto1(true);
    }

    return(
        <Container2>
            <h2>Escolha um tema</h2>
            <ul>
                <li onClick={show1}>Introdução</li>
                {/* <li onClick={show2}>Projeto 1</li> */}
            </ul>

            {reactjs1 && <ReactJS1 />}
            {projeto1 && <Projeto1 />}
            
        </Container2>
    );
}

export default ReactjsContent;