import styled from "styled-components";

export const Container = styled.div`
    margin-top: var(--navHeight);
    padding: 5rem 2rem;
    font-size: 1.7rem;
    text-align: justify;
    max-width: 110rem;
    margin-inline: auto;
    line-height: 2.2rem;
    letter-spacing: .2rem;

    li {
        padding-bottom: .5rem;
    }

    .images {
        padding-block: 2rem;
        width: 100%;
        display: flex;
        align-items: center;
        flex-direction: column;
        gap: 2rem;

        img {
            width: 70rem;
            max-width: 85vw;
        }
    }

    a {
        text-decoration: none;
        color: var(--headerColor);

        :hover {
            text-decoration: underline;
        }
    }
`;