import { Container } from "./styles";
import { Video } from "../videos";

const LoopWhile = () => {
    const abrir = "{";
    const fechar = "}";
    const menorque = "<";
    const usuarioNome = "${usuario.nome}";
    const usuarioIdade = "${idade}";

    return(
        <Container>
            <h2>Loop While</h2>

            <p>O loop while é parecido com o loop for, na maioria das vezes você pode escolher qual dos dois usar, mas vamos entender como o while funciona.</p>

            <p>Diferente do for, no while criamos a variável fora, e o parâmetro informa que enquanto o requisito não for satisfeito a ordem será executada, veja no nosso exemplo abaixo que adicionamos "idade++", isse é para que o valor da variável vá aumentando assim como a ordem do terceiro parâmetro do loop for, e caso você não adicione "idade++" o loop será executado infinitamente até que seu navegador trave ou você consiga fechá-lo, então não esqueça de adicionar "idade++".</p>

            <div className="function">
                <p>let idade = 0;</p>

                <p>
                    <span>while (idade {menorque}= 10) {abrir}</span><br />
                    <span className="spanSpace">console.log(`A idade é {usuarioIdade} anos.`);</span><br />
                    <span className="spanSpace">idade++;</span><br />
                    <span>{fechar}</span>
                </p>
            </div>

            <p>O loop while acima tem o mesmo efeito do loop for abaixo, faça uma comparação dos dois.</p>    

            <div className="function">
                <p>
                    <span>for (let idade = 0; idade {menorque}= 10; idade++) {abrir}</span><br />
                    <span className="spanSpace">console.log(`A idade é {usuarioIdade} anos.`);</span><br />
                    <span>{fechar}</span>
                </p>
            </div>        

            <p>Agora que você conhece os loops eu te desafio a fazer alguns exemplos de loop for e depois recriá-los com o loop while. Te espero na próxima aula.</p>


            {/* <h3>Assista essa aula para entender melhor</h3>

            <Video link='https://www.youtube.com/embed/E-p5NoFjFOo' /> */}

        </Container>
    );
}

export default LoopWhile;