import { Container } from "./styles";
import { Link } from "react-router-dom";
import { Video } from "../videos";
import IMG1 from '../../assets/projeto1img3.jpg';
import IMG2 from '../../assets/projeto1img4.jpg';
import IMG3 from '../../assets/projeto1img5.jpg';

const Projeto1 = () => {
    return(
        <Container>
            <h2>Projeto 1</h2>

            <h3>Iniciando estilização</h3>
            <p>Dando continuidade ao Projeto 1, vamos iniciar com algumas configurações gerais para deixar as margens: 0 e paddings: 0, com isso fica mais fácil de posicionar os itens exatamente onde queremos, o box-sizing: border-box e a font-family: Arial.</p>

            <div className="images">
                <img src={IMG1} alt="imagem com exemplo de código CSS" />
            </div>

            <p>No "body" adicionamos um max-width: 1100px, isso significa que mesmo em telas maiores o conteúdo do body não ultrapassa os 1100px. Para centralizar o body foi adicionada a margin-inline: auto, dessa forma mesmo que a largura da tela aumentar o preenchimento da margem lateral vai aumentando também e assim o body continua centralizado. Para deixar as letras um pouco mais separadas foi utilizado o letter-spacing: 1px, então teremos 1px de espaço entre as letras.</p>

            <div className="images">
                <img src={IMG2} alt="imagem com exemplo de código CSS" />
            </div>

            <p>No resto do conteúdo temos a utilização do display: flex e justify-content para posicionar os itens no local desejado.</p>

            <div className="images">
                <img src={IMG3} alt="imagem com exemplo de código CSS" />
            </div>

            <p>Vamos dar uma atenção especial para o uso do "@media screen" na linha 83 do código css, com esse comando nós podemos definir configurações que serão adicionadas a partir de uma determinada quantidade de pixels com min-width ou até uma quantidade de pixels com o max-width. Normalmente fazemos o uso dessa media query para diferenciar a estilização do conteúdo mobile para o conteúdo desktop. Lembrando que cada dispositivo tem seu tamanho de tela em px.</p>

            <p>Com esse pequeno projeto vimos como utilizar algumas tags importantes e estilizações que utilizamos no dia a dia enquanto programamos.</p>

            <p>Agora que você já viu assuntos sobre HTML e CSS tente fazer um de nossos <Link to={"/dpv/exercicios"}>exercícios</Link>, botar a mão no código sempre é a melhor forma de aprender.</p>

            <h3>Veja o vídeo para entender melhor a criação do código</h3>
            <Video 
                link="https://www.youtube.com/embed/h4ds7bZNDAA"
            />

        </Container>
    );
}

export default Projeto1;