import { Container } from "./styles";
import { useNavigate } from "react-router-dom";

const BackButton = () => {
    const navigate = useNavigate();

    const handleBackButton = () => {
        navigate(-1);
    }

    return(
        <Container>
            <button
                onClick={handleBackButton}
            >Voltar</button>
        </Container>
    );
}

export default BackButton;