import { Container } from "./styles";
import { Link } from "react-router-dom";
import IMG1 from '../../assets/projeto1img1.jpg';
import IMG2 from '../../assets/projeto1img2.jpg';
import { Video } from "../videos";

const Projeto1 = () => {
    return(
        <Container>
            <h2>Projeto 1</h2>

            <h3>Criando o projeto</h3>
            <p>Primeiro passo é criar uma a pasta que irá guardar nosso projeto, após escolher/criar a pasta clique com o botão direito e abra a pasta com o VSCode. Caso você tenha dificuldades em entender algum termo utilizado aqui recomendo que dê uma olhada na nossa página de "<Link to={"/dpv/introduce"} >introdução</Link>", nela você ensinamos o que é necessário saber antes de iniciar a criar códigos.</p>

            <p>Agora com o projeto criado e aberto no editor de códigos, crie a estrutura básica inicial para iniciar seu código, lembre-se que temos o atalhos e ao digitar "!" o VSCode nos mostra opções com estruturas para iniciar-mos.</p>

            <div className="images">
                <img src={IMG1} alt="imagem com exemplo de código HTML" />
            </div>

            <p>Acima temos o nosso "nav" criado com um "h2" e uma "div" com alguns links dentro. Veja que dentro dos links temos alguns "SVGs". Um "SVG" é um formato de imagem que está em código puro, e nesse caso são ícones do Instagram, Facebook e Youtube que baixei no site <Link to={"https://www.svgrepo.com/"}>svgrepo.com</Link>. Quando entrar no site você pode pesquisas o ícone pelo nome e escolher o que deseja utilizar e fazer o download. Após realizar o download, coloque o arquivo no seu projeto assim como no exemplo acima. O arquivo será um pouco extenso dependendo da image, mas não se preocupe, normalmente os códigos dos SVGs são bem grandes.</p>

            <div className="images">
                <img src={IMG2} alt="imagem com exemplo de código HTML" />
            </div>

            <p>Agora criamos o "main" e o "footer". Na tag "h1" coloque o seu nome o nome de algum personagem para seu projeto. Já na tag "p" você pode escolher digitar algo ou apenas inserir um "Lorem", quando digitamos Lorem no editor de códigos ele insere um texto alternativo, caso você digite Lorem100 ele irá inserir um texto alternativo com 100 palavras, e assim por diante.</p>

            <p>A imagem utilizada (Homem careca fumando charuto) é um "png" para que facilite no visual do nosso site. Você pode inserir uma foto sua no formato que desejar.</p>

            <p>Agora que estamos com a parte do HTML inserida, vamos para a continuação no CSS. Te vejo lá!</p>

            <h3>Veja o vídeo para entender melhor a criação do código</h3>
            <Video 
                link="https://www.youtube.com/embed/0x3d5hrtB9M"
            />

        </Container>
    );
}

export default Projeto1;