import { useRoutes } from 'react-router-dom';
import Home from "../pages/home";
import Introduce from '../pages/tecs/introduce';
import HTML from "../pages/tecs/html";
import CSS from "../pages/tecs/css";
import Javascript from "../pages/tecs/javascript";
import Reactjs from "../pages/tecs/reactjs";
import Github from "../pages/tecs/github";
import Exercicios from "../pages/tecs/exercicios";
import NotFound from "../pages/notFound";
import Festival from '../components/exercicios/festival';
import Product from '../components/exercicios/product';
import Menu from '../components/exercicios/menu';
import Grid from '../components/exercicios/grid';
import Paralax from '../components/exercicios/paralax';

const MainRoutes = () => {
    return useRoutes([
        { path: '/site-devparatodos/', element: <Home /> },
        { path: '/site-devparatodos/introduce', element: <Introduce /> },
        { path: '/site-devparatodos/html', element: <HTML /> },
        { path: '/site-devparatodos/css', element: <CSS /> },
        { path: '/site-devparatodos/javascript', element: <Javascript /> },
        { path: '/site-devparatodos/reactjs', element: <Reactjs /> },
        { path: '/site-devparatodos/github', element: <Github /> },
        { path: '/site-devparatodos/exercicios', element: <Exercicios /> },
        { path: '/site-devparatodos/exercicios/festival', element: <Festival /> },
        { path: '/site-devparatodos/exercicios/product', element: <Product /> },
        { path: '/site-devparatodos/exercicios/menu', element: <Menu /> },
        { path: '/site-devparatodos/exercicios/grid', element: <Grid /> },
        { path: '/site-devparatodos/exercicios/paralax', element: <Paralax /> },

        { path: '*', element: <NotFound /> }
    ]);
}

export default MainRoutes;