import { Container } from "./styles";
import { Video } from "../videos";

const EventoTeclado2 = () => {
    const texto1 = 'const soltar = (event) => {';
    const texto2 = "console.log(event.code);";
    const fechar = '}';
    const texto3 = "const input = document.querySelector('input');";
    const texto4 = "input.addEventListener('keyup', soltar);";
    const texto5 = "console.log(event.key);";
    const texto6 = "console.log(event.shiftKey);";

    return(
        <Container>
            <h2>Eventos de teclado 2</h2>

            <p>Agora vamos ver mais alguns eventos de teclado, o "event.code", "event.key", "event.shiftKey", "event.altKey" e o "event.ctrltKey".</p>

            <p>Com o "event.code" nós conseguimos ver qual tecla foi apertada, se você apertar a tecla "n" o valor apresentado será "KeyN", veja no exemplo a seguir em que criamos uma função que está pegando o evento como parâmetro e dele estamos tirando o "code". Essa forma de verificar a tecla consegue diferenciar o shift esquedo do direito, já o "event.key" não consegue.</p>

            <div className="function">
                <p>
                    <span>{texto1}</span><br />
                        <span className="spanSpace">{texto2}</span><br />
                    <span>{fechar}</span><br />
                    <span>{texto3}</span><br />
                    <span>{texto4}</span><br />
                </p>
            </div>

            <p>Já com o "event.key" conseguimos ver o valor da tecla apertada, se você apertar o "n" o valor apresentado será "n". Lembrando que se houver duas teclas iguais como os "shift", o "event.key" não irá diferenciar as duas.</p>

            <div className="function">
                <p>
                    <span>{texto1}</span><br />
                        <span className="spanSpace">{texto5}</span><br />
                    <span>{fechar}</span><br />
                    <span>{texto3}</span><br />
                    <span>{texto4}</span><br />
                </p>
            </div>

            <p>Com esse próximo, o "event.shiftKey", iremos conseguir ver se a tecla "shift" estava acionada quando alguma tecla foi apertada. O resultado será um boolean, podendo ser true ou false.</p>

            <div className="function">
                <p>
                    <span>{texto1}</span><br />
                        <span className="spanSpace">{texto6}</span><br />
                    <span>{fechar}</span><br />
                    <span>{texto3}</span><br />
                    <span>{texto4}</span><br />
                </p>
            </div>

            <p>Da mesma forma que fizemos com o "event.shiftKey", podemos fazer com o "event.altKey" e o "event.ctrltKey". Esses eventos são mais utilizados em jogos ou em casos muitos específicos, mas é bom você saber como eles funcionam, pois cada coisa criada na programação tem sua serventia.</p>

            <p>Aproveita e usa esses últimos dois para criar seus exemplos de revisão. Te vejo na próxima aula.</p>
            
            {/* <h3>Assista essa aula para entender melhor</h3>

            <Video link='https://www.youtube.com/embed/E-p5NoFjFOo' /> */}

        </Container>
    );
}

export default EventoTeclado2;