import { Container } from "./styles";
import { Video } from "../videos";

const EventoTeclado = () => {
    const texto1 = 'const apertar = () => {';
    const texto2 = "console.log('tecla apertada');";
    const texto3 = 'const segurar = () => {';
    const texto4 = "console.log('tecla segurada');";
    const texto5 = "const soltar = () => {";
    const texto6 = "console.log('tecla liberada');";
    const texto7 = "document.addEventListener('keydown', apertar);";
    const texto8 = "document.addEventListener('keypress', segurar);";
    const texto9 = "document.addEventListener('keyup', soltar);";
    const fechar = '}';

    const texto10 = "const btn = document.querySelector('button');";
    const texto11 = "btn.addEventListener('click', () => {";
    const texto12 = "console.log('botão clicado');";
    const texto13 = "});";

    return(
        <Container>
            <h2>Eventos de teclado</h2>

            <p>Assim como temos o evento de clique, também temos os eventos de teclado que são acionados ao você apertar as teclas.</p>

            <p>Vamos dividir essa aula entre "keyDown" que é quando você aperta, "keyPress" que é o momento em que a tecla está sendo apertada, e "keyUp" que é quando você solta a tecla.</p>

            <p>No exemplo abaixo temos os três eventos que falamos, e cada um está acionando uma função que inserimos um console.log com uma mensagem.</p>

            <div className="function">
                <p>
                    <span>{texto1}</span><br />
                        <span className="spanSpace">{texto2}</span><br />
                    <span>{fechar}</span>
                </p>
                <p>{texto7}</p>
            </div>

            <div className="function">
                <p>
                    <span>{texto3}</span><br />
                        <span className="spanSpace">{texto4}</span><br />
                    <span>{fechar}</span>
                </p>
                <p>{texto8}</p>
            </div>

            <div className="function">
                <p>
                    <span>{texto5}</span><br />
                        <span className="spanSpace">{texto6}</span><br />
                    <span>{fechar}</span>
                </p>
                <p>{texto9}</p>
            </div>

            <p>Outra forma de acionar esse eventos é através do addEventListener, ele funciona como um escutador/monitorador, ele pede dois parâmetros, o primeiro é o evento que ele deve monitorar e o segundo a função/ação que ele deve executar. Veja no exemplo abaixo em que colocamos para monitorar quando alguma tecla for solta e ele vai executar a função soltar.</p>

            <p>{texto9}</p>

            <p>Como estamos testando, o addEventListener vai monitorar a partir de todo o documento, por isso ele começa com "document". Caso você queira que ele monitora apenas um elementos você deve informar</p>

            <div className="function">
                <p>
                    <span>{texto10}</span><br />
                    <span>{texto11}</span><br />
                        <span className="spanSpace">{texto12}</span><br />
                    <span>{texto13}</span>
                </p>
            </div>

            <p>Até agora você já viu bastante conteúdo, não esquece de dar aquela praticada. Te vejo na próxima aula.</p>
            
            {/* <h3>Assista essa aula para entender melhor</h3>

            <Video link='https://www.youtube.com/embed/E-p5NoFjFOo' /> */}

        </Container>
    );
}

export default EventoTeclado;