import { Container } from "./styles";

import IMG1 from '../../assets/vscode.jpg';
import IMG2 from '../../assets/vscode2.jpg';
import IMG3 from '../../assets/vscode3.jpg';
import { Link } from "react-router-dom";

const IntroduceContent = () => {
    return(
        <Container>
            <h3>Explicações iniciais</h3>
            <p>No mundo da programação existem vários tipos de tecnologia que usamos para desenvolver códigos de projetos que são frontend, backend, fullstack, mobile, entre outros. Aqui iremos iniciar com tecnologias frontend básicas para deixar uma boa base de entendimento para quem quer entrar no mundo da programação.</p>

            <p>De início iremos nos habilitar para fazer o uso de códigos ".html", ".css" e ."js(javascript)"</p>

            <h3>Coisas que precisamos</h3>
            <p>1 - editor de códigos (Visual Studio Code que é gratuito e um dos mais conhecido atualmente). Ele não é a única forma de editar códigos HTML, até com o bloco de notas podemos escrever um código e abrir ele no navegador, basta ele estar em arquivo .html. A vantagem de um editor de códigos é que eles nos ajudam na hora de criar o código com auto preenchimento em algumas partes e também destacam os tipos de informações com cores diferentes para facilitar a identificação tanto de algo que já procuramos quanto de erros aparentes em nosso código.</p>

            <div className="images">
                <img src={IMG1} alt="imagem do editor Visual Studio Code" />
            </div>

            <p>2 - para adquirir o VSCode precisamos entrar no site dele e baixar para instalação. Ao entrar em "<Link target={"_blank"} to="https://code.visualstudio.com/" >https://code.visualstudio.com/</Link>" automaticamente o sistema que você usa será identificado (Windows, Mac ou Linux) e será disponibilizado um botão para download e você irá baixar a versão Stable Build (que normalmente já está selecionada). Assim que baixar é só fazer a instalação e depois de concluída é só abrir e usá-lo.</p>

            <div className="images">
                <img src={IMG2} alt="imagem do site Visual Studio Code" />
            </div>

            <p>3 - Agora vamos criar nosso primeiro arquivo para poder editar os códigos através do VSCode. Para manter a organização é recomendável que você crie uma pasta em um local específico para ir guardando seus projetos criados. Como exemplo de explicação iremos criar essa pasta na área de trabalho clicando com o botão direito do mouse e depois em Novo e iremos dar o nome a essa pasta de projetos. Depois entre na pasta projetos e crie outra pasta com o nome " projeto-1 ", depois de criada você irá clicar com o botão direito na pasta projeto-1 e clicar na opção de abrir com Code. Pronto, agora já temos nosso projeto criado e disponível para edição do arquivo HTML, CSS e Javascript.</p>

            <div className="images">
                <img src={IMG3} alt="imagem com exemplo de código HTML" />
            </div>

            <p>4 - Essa é uma das formas que existem de criar um projeto para uma página Web. Mais a frente iremos ver outras formas de criar projetos que serão para utilizar com outras tecnologias, e para isso iremos conhecer o uso do NODEJS que irá nos auxiliar muito no processo.</p>

        </Container>
    );
}

export default IntroduceContent;