import { Container } from "./styles";
import { Video } from "../videos";

const ManipulandoDom3 = () => {
    const texto1 = 'function clicar () {';
    const texto2 = "let ul = document.querySelector('ul');";
    const texto3 = "const h2 = document.createElement('h2');";
    const texto4 = "h2.innerHTML = 'Clicado'";
    const texto5 = 'ul.before(h2);';
    const texto6 = '}';
    const texto7 = 'ul.after(h2);';

    return(
        <Container>
            <h2>Manipulando o DOM 3</h2>

            <p>Vamos aprender agora como criar e adicionar elementos antes e depois do elemento selecionado com o "before" e o "after". Veja no exemplo abaixo que estamos selecionando o elemento "ul", criando outro elemento com "createElement('h2')", adicionando um conteúdo ao elemento criado com o "innerHTML" e por fim dando o comando "before". Esse comando "before" significa que o "h2" criado será adicionado antes do elemento "ul" selecionado anteriormente.</p>

            <div className="function">
                <p>
                    <span>{texto1}</span><br />
                    <span className="spanSpace">{texto2}</span><br />
                    <span className="spanSpace">{texto3}</span><br />
                    <span className="spanSpace">{texto4}</span><br />
                    <span className="spanSpace">{texto5}</span><br />
                    <span>{texto6}</span>
                </p>
            </div>

            <p>Caso queria adicionar o elemento novo após o elemento selecionado você deve usar o comando "after", como no exemplo abaixo.</p>

            <div className="function">
                <p>
                    <span>{texto1}</span><br />
                    <span className="spanSpace">{texto2}</span><br />
                    <span className="spanSpace">{texto3}</span><br />
                    <span className="spanSpace">{texto4}</span><br />
                    <span className="spanSpace">{texto7}</span><br />
                    <span>{texto6}</span>
                </p>
            </div>

            <p>Tenta fazer alguns exemplos, e nos vemos na próxima aula.</p>

            {/* <h3>Assista essa aula para entender melhor</h3>

            <Video link='https://www.youtube.com/embed/E-p5NoFjFOo' /> */}

        </Container>
    );
}

export default ManipulandoDom3;