import styled from "styled-components";

export const Container = styled.div`
    margin-top: var(--navHeight);

    width: 100%;
    height: 60vh;
    padding-block: 1rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 15rem;
    font-weight: bold;
    color: var(--color2);

    .notFound {
        display: flex;
        align-items: center;
        gap: 1rem;
    }

    .zero {
        width: 10rem;
        height: 10rem;
        border-radius: 100%;
        border: 2px solid black;
        background-color: var(--borderColor);
        display: flex;
        justify-content: space-evenly;
        padding-top: 2rem;
    }

    .circleLeft {
        width: 4rem;
        height: 5rem;
        border: 2px solid black;
        border-radius: 100%;
    }

    .circleRight {
        width: 4rem;
        height: 5rem;
        border: 2px solid black;
        border-radius: 100%;
    }

    .ball {
        width: 2.2rem;
        height: 2.2rem;
        background-color: var(--color2);
        border-radius: 100%;
        margin-top: 2rem;
        animation: move 3s infinite;
    
    }

    @keyframes move {
        0% {
            margin-left: 0;
        } 50% {
            margin-left: 1.9rem;
        } 100% {
            margin-left: 0;
        }
    }

    .message {
        font-size: 4rem;
        text-align: center;
    }

    .btn {
        margin-top: 4rem;
        width: 15rem;
        height: 5rem;
        font-size: 2.5rem;
        background-color: var(--color2);
        color: var(--color1);
        border: 2px solid var(--color2);
        border-radius: 3rem;
        cursor: pointer;

        :hover {
            background-color: var(--color1);
            color: var(--color2);
            font-weight: bold;
        }
    }
`;