import styled from "styled-components";

export const Container = styled.div`
    padding: 30px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    .text {
        height: 720px;
        width: 350px;
        background-color: rgb(252, 231, 231);
        padding: 1rem;
        border-radius: 2rem;
        box-shadow: 0 0 20px rgba(0, 0, 0, 0.41);
        margin-bottom: 4rem;
    }

    p {
        padding-block: 12px;
        font-size: 16px;
        color: #3d3d3d;
    }

    h1,
    h3 {
        color: #3d3d3d;
    }

    h2 {
        font-size: 30px;
        color: var(--headerColor);
    }

    h4 {
        text-decoration: line-through;
        color: rgb(182, 9, 9);
    }

    .img {
        width: 100%;
    }

    .img img {
        width: 100%;
        border-radius: 20px 20px 0 0;
    }

    .descricao {
        padding: 20px;
        display: flex;
        flex-direction: column;
    }

    .price {
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 10px;
        padding-bottom: 10px;
        height: 50px;
    }

    button {
        margin: auto;
        width: 300px;
        height: 50px;
        display: flex;
        align-items: center;
        justify-content: space-around;
        font-size: 20px;
        font-weight: bold;
        border-radius: 25px;
        border: none;
        cursor: pointer;
        background-color: rgb(180, 180, 255);
        color: rgb(3, 3, 112);
    }

    button:hover {
        text-decoration: underline;
    }

    button img {
        height: 100%;
    }
/* ------------------- */
    .btn {
        width: 8rem;
        height: 3rem;
        margin-inline: auto;
        background-color: var(--headerColor);
        color: var(--color1);
        border: 1px solid var(--headerColor);
        border-radius: 2rem;
        cursor: pointer;
        font-weight: normal;
        font-size: 1.5rem;
        
        :hover {
            background-color: var(--color1);
            color: var(--headerColor);
            text-decoration: none;
        }
    }

    .box {
        max-width: 35rem;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .image {
        width: 100%;
        border: 1px solid var(--borderColor);
        border-radius: 2rem;
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;

        img {
            width: 100%;
            border-radius: 2rem;
            cursor: pointer;
        }
    }
`;