// import IMG5 from '../../assets/react5.jpg';
import { Link } from "react-router-dom";

const GithubContent = () => {
    return(
        <Container>
            <h3>O que é Git?</h3>

            <p>o git serve para que você vá versionando seu código, isso significa que você tem a opção de a cada alteração feita em seu código você vá salvando a versão alterada e se você criar uma parte nova do código que apresente algum erro que você não consiga identificar pra resolver, você poderá voltar para a versão salva anteriormente antes de ser feito o código que apresenta erro. Praticamente é uma forma de garantir que você não perca todo o código por causa de um pequeno erro.</p>

            <p>Outra forma de ajuda que o GIT oferece com o versionamento é a possibilidade de mais de uma pessoa trabalhar no mesmo projeto e toda vez que cada uma salvar sua alteração, por ela estar versionada, o arquivo pode ser retornado para a data de antes da apresentação do erro e sem atrapalhar os colegas. Tudo sempre com um histórico apresentando as alterações.</p>

            <p>Caso duas pessoas alterem a mesma parte do código será apresentado um conflito, isso ajuda para que um não apague ou sobrescreva alguma coisa que outra pessoa criou.</p>

            <h3>Como instalar o GIT?</h3>

            <p>Se direcione ao site <Link to='https://git-scm.com/downloads' target="_blank" >https://git-scm.com/downloads</Link> e escolher a verão para seu sistema operacional, tendo a opção de Windows, MacOS e Linux.</p>

            <p>Baixe o instalador e faça a instalação na sua máquina.</p>

            <p>Após instalado reinicie o seu prompt de comando e dê o comando "git --version" para ver a versão instalada.</p>

            <h3>Configurando o GIT.</h3>

            <p>Agora vamos configurar o GIT com nossos dados para poder ficar registrado toda ver que fizermos algo no GIT, como uma alteração por exemplo.</p>

            <p>No prompt vamos dar o seguinte comando para definir seu nome: git config --global user.name "seu nome aqui"</p>

            <p>O seguinte comando para seu email: git config --global user.email "seu email aqui"</p>

            <p>O seguinte comando para informar qual editor de código você está usando: git config --global core.editor nome-do-editor-de-código</p>

            <p>Agora vamos aprender a ver quais as informações cadastradas: git config --list</p>

            {/* <div className="images">
                <img src={IMG1} alt="Imagem do site do NodeJS" />
            </div> */}

            {/* <div className="images">
                <img src={IMG2} alt="Imagem do prompt de comando mostrando as versões do NodeJS e do NPM" />
            </div> */}
            
            {/* <div className="images">
                <img src={IMG3} alt="Imagem do VSCode mostrando o projeto criado" />
            </div> */}

            {/* <div className="images">
                <img src={IMG4} />
            </div> */}

            {/* <div className="images">
                <img src={IMG5} />
            </div> */}

        </Container>
    );
}

export default GithubContent;