import { Container } from "./styles";
import { Video } from "../videos";

const Comentario = () => {
    return(
        <Container>
            <h2>Comentário no código Javascript</h2>

            <h3>Entendendo o que é um comentário</h3>
            <p>Quando estamos trabalhando em nosso projeto, seja em um documento Javascript, HTML ou até mesmo CSS, nós iremos muitas vezer precisar comentar uma parte do código para verificar alguma coisa. Ao comentar um trecho de código ele não será renderizado no navegador, isso significa que apenas será possível ver o trecho comentado no código.</p>

            <p>Para comentar uma linha de código Javascript só é necessário inserir duas barras no início dela (//), assim toda a linha será comentada. Caso você insira as duas barras no meio de uma linha de código, apenas o conteúdo após as barras irá ser comentado (let x = 10 // esse é um comentário)</p>

            <h3>Teclas de atalho</h3>

            <p>Para facilitar nossa vida, podemos selecionar uma ou mais linhas e utilizar as teclas de atalho "CTRL + ;" e assim a linha ou linhas selecionadas serão comentadas. Isso ajuda muito, pois em algumas ocasiões queremos saber como o projeto se comporta sem algumas linhas de código, então nós as comentamos para ver o resultado.</p>

            <h3>Assista essa aula para entender melhor</h3>

            <Video link='https://www.youtube.com/embed/A_Hb7xTnfpM' />

        </Container>
    );
}

export default Comentario;