import { Container } from "./styles";
import IMG1 from '../../assets/html/projeto2/projeto2img6.jpg';
import { Link } from "react-router-dom";

const ProjetoJs2 = () => {
    return(
        <Container>
            <h2>Criando o Javascript do projeto 2</h2>

            <p>Bom, se você chegou até aqui então já passou pelo HTML e CSS do projeto 2, caso não tenha feito, recomendo que você volte para fazer, pois precisamos do HTML e CSS desse projeto para fazer com que ele funcione como queremos.</p>

            <p>Lembrando que por estarmos iniciando nossos estudos o nosso código Javascript estará bastante autoesplicativo, e lembrando também que cada linha de código tem sua função, então caso seu código não funcione você deve revisar o conteúdo para vorrigir eventuais error. Então let's go!</p>

            <div className="images">
                <img src={IMG1} alt="imagem com exemplo de código CSS" />
            </div>

            <h3>EventListener</h3>

            <p>Na linha 1 temos o comando element.addEventListener('click', clicou), vamos entender o que significa pois esse é um comando muito utilizado no dia a dia do programador. O "addEventListener" diz praticamente o seguinte, quando houver o evento de "click" no meu projeto eu quero que você observe a função "clicou" para executá-la.</p>

            <h3>Function clicou(n)</h3>

            <p>Na linha 3 temos uma função que está recebendo um parâmetro, se você voltar lá na parte de criação do HTML, você verá que informamos esse valor em todos os botões de notas (1, 2, 3, 4 e 5) dentro das funções "clicou(parâmetro)", assim saberemos qual botão está sendo clicado. </p>

            <h3>Variáveis (var)</h3>

            <p>Var é uma forma de usar variáveis no Javascript, não é a mais recomendada, mas estamos utilizando ela aqui para você entender que ela existe. Uma variável pode receber um valor e depois ter seu valor alterado, nos casos das linhas 4 a 8 temos as variáveis recebendo os "id" que colocamos lá no HTML dos botões.</p>

            <h3>Variáveis (let)</h3>

            <p>Agora vamos ver outra forma de variável, a "let", que é uma variável de escopo local, isso significa que criamos esse tipo de variável dentro de uma função para usar o valor que ela recebe apenas nessa função. Temos uma "let" na linha 10.</p>

            <h3>GetElemenById</h3>

            <p>Essa é uma das formas que temos para referênciar uma tag HTML dentro do Javascript, dessa forma estamos buscando a tag através do "id" que ela tem.</p>

            <h3>QuerySelector</h3>

            <p>O querySelector é outra forma de referênciar uma tag HTML no Javascript, mas essa forma é mais avançada que o simples getElementById, pois o querySelector pode pegar a tag por "id", "class", "tag" e outras formas, mas as mais utilizadas são essas três.</p>

            <h3>If e else if</h3>

            <p>Traduzindo para o português, "if" é "se", e "else if" é "senão se". Essa é uma forma que usamos muito para dizer o seguinte:</p>

            <p> se(condição) execute isso, <br />
            senão se(condição) execute isso, <br />
            senão sxecute isso.</p>

            <p>Não é recomendado pelas boas práticas que se use muitos "if else" no código, mas para um início na programação temos que aprender como usar o "if" e depois nos preocuparemos com formas mais avançadas.</p>

            <h3>Window.location.href</h3>

            <p>Esse comando que estamos utilizando na função da linha 49 está dizendo o seguinte, quando essa função for executada você irá mudar para a página que estou indicando aqui. Lembrando que podemos mudar de página tando com o HTML como com o Javascript, escolher qual usar vai depender o projeto que você está criando.</p>

            <p>Chegamos ao fim desse projeto, tente fazer e entender cada passo. Lembrando que temos mais explicações em outros projetos aqui no DevParaTodos. Aproveita e põe em prática o que você está aprendendo com nossos <Link to='/site-devparatodos/exercicios'>exercícios</Link>. Até mais!</p>

        </Container>
    );
}

export default ProjetoJs2;